(function() {
    'use strict';
    angular
        .module('app.auth')
        .factory('MicroserviceFactory', MicroserviceFactory);

    function MicroserviceFactory() {
        var microservices = [];
        var user = [];

        var APAdmin = function() {
            if (user.type == "DB")
                return true;

            var roles = [];
            for (var i = 0; i < microservices.length; i++) {
                roles = microservices[i].roles;
                if (microservices[i].name == "efadminpanel") {
                    for (var j = 0; j < roles.length; j++) {
                        if (roles[j].name == "admin") {
                            return true
                        }
                    }
                    return false;
                }
            }
        };
        var getRoles = function(micro) {
            var roles = [];
            var value = [];
            for (var i = 0; i < microservices.length; i++) {
                roles = microservices[i].roles;
                if (microservices[i].name == micro) {
                    for (var j = 0; j < roles.length; j++) {
                        value.push(roles[j].name)
                    }
                    return value;
                }
            }
        };
        var getPermissions = function(micro) {
            var permissions = [];
            var roles = [];
            var value = [];
            for (var i = 0; i < microservices.length; i++) {
                roles = microservices[i].roles;
                if (microservices[i].name == micro) {
                    for (var j = 0; j < roles.length; j++) {
                        permissions = roles[j].permissions;
                        for (var k = 0; k < permissions.length; k++) {
                            value.push(permissions[k].expression)
                        }
                    }
                    return value;
                }
            }
        };
        return {
            setMicroservices: function(data) {
                angular.forEach(data, function(arr) {
                    microservices.push(arr);
                });
            },
            getMicroservices: function() {
                return microservices;
            },
            setUser: function(data) {
                user = data;
            },
            getUser: function() {
                return user;
            },
            deleteAll: function() {
                microservices.length = 0;
                user.length = 0;
            },
            getRoles: getRoles,
            getPermissions: getPermissions,
            APAdmin: APAdmin

        }
    }
})();
