(
    function() {
        'use strict';

        angular
            .module('app.utilsCustom')
            .directive('fetchFiles', ['$rootScope', fetchFiles])
            .controller('fecthFilesController', ['MicroserviceFactory', 'utilCustom', 'PromptService', 'ScriptService',  '$filter', '$scope', fecthFilesController])


        function fetchFiles($rootScope) {

            return {
                restrict: 'EA',
                scope: {
                    addArray: '=addFile',
                    typeOfFile: '=typeOfFile',
                    format: '=formatFile',
                    maxFiles : '=maxFiles'
                },
                templateUrl: 'app/adminPanel/fetchFilesTempalte.html',
                controller: 'fecthFilesController as vm',
                transclude: false
            }

        }


        function fecthFilesController(MicroserviceFactory, utilCustom, PromptService, ScriptService, $filter, $scope) {
            var vm = this;

            vm.openFolder = openFolder;
            vm.openMenu = openMenu;
            vm.folderTriverseArray = [];
            vm.noOfFiles = 0;
            vm.backFolder = backFolder;
            vm.addFile = addFile;
            vm.addArray = $scope.addArray;
            vm.typeOfFile = $scope.typeOfFile ? $scope.typeOfFile : 'prompt';
            vm.format = Array.isArray($scope.addArray) ? 'multiple' : 'singel'

            function openMenu() {
                if (!vm.prompts)
                    vm.typeOfFile === 'prompt' ? fetchPrompt('first', 'yes') : fetchScript('first', 'yes');
            }

            function openFolder(folderName, insertPath) {
                var fldNm = JSON.stringify({ name: folderName });
                var splitPath = folderName.split('/');
                if (splitPath[splitPath.length - 1] != folderName) {
                    if (splitPath.length == 3 && setTeamName() != 'all') fldNm = JSON.stringify({ name: folderName });
                    vm.typeOfFile === 'prompt' ? fetchPrompt(fldNm, insertPath) : fetchScript(fldNm, insertPath);
                }

            }

            function setProperties(response, params) {

              vm.prompts = {Folder:'', File : '' , path : '' , data : ''};

                vm.prompts['data'] = (vm.typeOfFile === 'prompt') ? response.Prompt : response.Script;
                if (response.path == "") {
                    response.path = "/";
                }
                vm.prompts['path'] = response.path;
                vm.folderTriverseArray.push(vm.prompts);
              if(vm.prompts.data)
                if(vm.prompts.data.hasOwnProperty('Folder'))
                vm.folders = vm.prompts.data.Folder ? vm.prompts.data.Folder : [];
              if(vm.prompts.data)
                if(vm.prompts.data.hasOwnProperty('File'))
                vm.files = vm.prompts.data.File ? vm.prompts.data.File : [];
                params.path = vm.prompts.path;
                if (params.path == "") {
                    params.path = ""
                }
                var pa = params.path;

                if (vm.files != undefined) {
                    vm.selected = vm.files[0];
                    if (vm.selected)
                        vm.selected['type'] = 'file';
                    //vm.sound = ngAudio.load("../app/adminPanel/prompt/welcome.mp3");

                } else if (vm.folders) {
                    vm.selected = vm.folders[0];
                    if (vm.selected) {
                        vm.selected.FileName = vm.selected.FolderName;
                        var datee = vm.selected.Details.dateModified;
                        vm.selected['type'] = 'Folder';
                        vm.selected.languages = [];
                    }

                } else {
                    vm.selected.path = params.path;
                }
            }


            function backFolder() {
                var size = vm.folderTriverseArray.length - 1;
                vm.folderTriverseArray.pop();
                vm.prompts = vm.folderTriverseArray[vm.folderTriverseArray.length - 1];
                vm.selecte = { path: vm.prompts.path };
            }

            function fetchPrompt(req, insertPath, teamName) {
                var params;
                req == "first" ? params = { path: '', team: 'all' } : params = { path: JSON.parse(req).name, team: 'all' };
                vm.params = params;
                vm.selected = { languages: [] };
                vm.params.team = setTeamName(teamName);
                utilCustom.toasterLoading();
                PromptService.list(params).then(function(response) {
                    utilCustom.hideToaster();
                    setProperties(response, params);


                }, function(error) {
                    utilCustom.hideToaster();
                    console.log(error);
                })


            }

            function fetchScript(req, insertPath, teamName) {
                var params;
                req == "first" ? params = { path: '', team: 'all' } : params = { path: JSON.parse(req).name, team: 'all' };
                vm.params = params;
                vm.selected = { languages: [] };
                vm.params.team = setTeamName(teamName);
                utilCustom.toasterLoading();
                ScriptService.list(params).then(function(response) {
                    utilCustom.hideToaster();
                    setProperties(response, params);


                }, function(error) {
                    utilCustom.hideToaster();
                    console.log(error);
                })


            }

            function addFile(file) {
              utilCustom.hideToaster();
                var selectedPath = "";
                if (vm.typeOfFile == 'prompt') {
                  var path = file.path.split("/").reverse()
                  path.pop();
                  path.pop();
                  selectedPath = path.reverse().join("/")
                }
                if (Array.isArray($scope.addArray)) {
                  var index = $scope.addArray.indexOf(selectedPath + file.FileName);

                  if (index == -1) {
                    if($scope.addArray.length < $scope.maxFiles){
                    $scope.addArray.push(selectedPath + file.FileName);


                    }
                    else{
                      utilCustom.toaster($filter('translate')('generic.maxNoOfFilesSelected'));
                    }
                  }
                  else{
                    utilCustom.toaster($filter('translate')('generic.alreadySelected'));

                  }
                } else {
                  if (vm.typeOfFile === 'script' && file.path.substring(0, 1) == '/') {
                    selectedPath = file.path.substring(1, file.path.length)
                  }
                  $scope.addArray = selectedPath + file.FileName;

                }

                // if (vm.format === 'multiple') {
                //     var index = $scope.addArray.indexOf(file.path + file.FileName);
                //     if (index == -1)
                //         $scope.addArray.push(selectedPath+ file.FileName);
                // } else {

                //     if (vm.typeOfFile === 'script' && file.path.substring(0, 1) == '/') {
                //         selectedPath = file.path.substring(1, file.path.length)
                //     }
                //     $scope.addArray = selectedPath + file.FileName;
                // }


              //}
              //else{ if(!vm.already)
              //  utilCustom.toaster($filter('translate')('generic.maxNoOfFilesSelected'));
              //}


            }

            function setTeamName(teamName) {
                var name = "all";
                if (!teamName) {
                    var team = window.localStorage.getItem('currentTeam');
                    if (!team || team == "undefined") {
                        if (!MicroserviceFactory.APAdmin()) {
                            // name = undefined;
                            vm.easyAnnouncemens = [];
                        }
                    } else {
                        team = JSON.parse(team);
                        name = team.name;
                    }
                } else {
                    name = teamName;
                }
                return name
            }

        }

    }
)();

(function ()
{
    'use strict';

    angular
        .module('fuse')
        .run(runBlock);

    /** @ngInject */
    function runBlock($rootScope, $timeout, $state)
    {
        // Activate loading indicator
        var stateChangeStartEvent = $rootScope.$on('$stateChangeStart', function ()
        {
            $rootScope.loadingProgress = true;
        });

        // De-activate loading indicator
        var stateChangeSuccessEvent = $rootScope.$on('$stateChangeSuccess', function ()
        {
            $timeout(function ()
            {
                $rootScope.loadingProgress = false;
            });
        });

        // Store state in the root scope for easy access
        $rootScope.$state = $state;

        // Cleanup
        $rootScope.$on('$destroy', function ()
        {
            stateChangeStartEvent();
            stateChangeSuccessEvent();
        })
    }
})();

(function() {
    'use strict';

    angular
        .module('fuse')
        .config(routeConfig);

    /** @ngInject */
    function routeConfig($stateProvider, $urlRouterProvider, $locationProvider) {
        $locationProvider.html5Mode(false);

        $urlRouterProvider.otherwise('/agent');

        /**
         * Layout Style Switcher
         *
         * This code is here for demonstration purposes.
         * If you don't need to switch between the layout
         * styles like in the demo, you can set one manually by
         * typing the template urls into the `State definitions`
         * area and remove this code
         */
        // Inject $cookies
        var $cookies;

        angular.injector(['ngCookies']).invoke([
            '$cookies',
            function(_$cookies) {
                $cookies = _$cookies;
            }
        ]);

        // Get active layout
        var layoutStyle = $cookies.get('layoutStyle') || 'verticalNavigation';

        var layouts = {
            verticalNavigation: {
                main: 'app/core/layouts/vertical-navigation.html',
                toolbar: 'app/toolbar/layouts/vertical-navigation/toolbar.html',
                navigation: 'app/navigation/layouts/vertical-navigation/navigation.html'
            },
            horizontalNavigation: {
                main: 'app/core/layouts/horizontal-navigation.html',
                toolbar: 'app/toolbar/layouts/horizontal-navigation/toolbar.html',
                navigation: 'app/navigation/layouts/horizontal-navigation/navigation.html'
            },
            contentOnly: {
                main: 'app/core/layouts/content-only.html',
                toolbar: '',
                navigation: ''
            },
            contentWithToolbar: {
                main: 'app/core/layouts/content-with-toolbar.html',
                toolbar: 'app/toolbar/layouts/content-with-toolbar/toolbar.html',
                navigation: ''
            }
        };
        // END - Layout Style Switcher

        // State definitions
        $stateProvider
            .state('app', {
                abstract: true,
                views: {
                    'main@': {
                        templateUrl: layouts[layoutStyle].main,
                        controller: 'MainController as vm'
                    },
                    'toolbar@app': {
                        templateUrl: layouts[layoutStyle].toolbar,
                        controller: 'ToolbarController as vm'
                    },
                    'navigation@app': {
                        templateUrl: layouts[layoutStyle].navigation,
                        controller: 'NavigationController as vm'
                    },
                    'quickPanel@app': {
                        templateUrl: 'app/quick-panel/quick-panel.html',
                        controller: 'QuickPanelController as vm'
                    },
                    'system@app': {
                        templateUrl: 'app/core/systemSetting/systemSetting.html'
                    }
                }
            });
    }

})();

(function ()
{
  'use strict';

  angular
    .module('fuse')
    .controller('IndexController', IndexController)
    .config(['$httpProvider',function($httpProvider){
      $httpProvider.interceptors.push([
        '$injector', function ($injector) {
          return $injector.get('AuthInterceptor');
        }
      ]);

    }]);

  window.appBaseUrl = 'http://localhost:9091/umm/efadminpanel';
  window.appBaseUrl2 = 'http://localhost:9091/umm';
  window.appBaseUrl3= 'http://localhost:8080/efadminpanel';


  /** @ngInject */
  function IndexController(MicroserviceFactory, applicationSettingService, $rootScope,fuseTheming,Session,utilCustom,$filter, AUTH_EVENTS, AuthService,$location,$scope,$state )
  {
    var vm = this;
    //  window.alpppBaseUrl = 'http://192.168.200.47:9092/AdminPanel';

    // Data
    vm.themes = fuseTheming.themes;
    checkApplicationEnvirnoment();

    vm.isAuthorized = AuthService.isAuthenticate();
    isAuthorizedIndex();

    if (vm.isAuthorized == false) {
      // AuthService.hideSideMenu();
      AuthService.serverAuthenticate().then(function () {

        $state.go("app.pages_auth_login");

      },function(error){
        $state.go("app.pages_auth_login");
      });
    }
    else {

      //   var currentPath = $location.path();
      Session.setAuthToken();
      AuthService.serverAuthenticate().then(function (userData) {
        vm.username = Session.username();
        MicroserviceFactory.setMicroservices(userData.userDetails.microservices);
        applicationSettingService.menuforAdminpanel(MicroserviceFactory.getMicroservices(), userData.enableEasyAnnouncement);
        //createSideMenu(userData.role);
        window.applicationEnvironment=='ecm'?   $state.go("app.campaign"):   $state.go("app.agent");
        utilCustom.hideToaster();


      });


    }

    function checkApplicationEnvirnoment(){
      switch (window.applicationEnvironment){
        case 'ecm':{vm.title="Campaign Manager";break;}
        case 'adminpanel':{vm.title="Admin Panel";break;}
        default: {vm.title="Admin Panel/ECM";}
      }
    }

    function isAuthorizedIndex() {
      if (AuthService.isAuthenticate() == false)
        $state.go("app.pages_auth_login");
    };
    vm.hasPermission = function (permission, id) {
      if (id != undefined)
        permission = permission.concat(":" + id);

      return PermissionService.hasPermission(permission);
    };
    vm.getUsername = function () {
      return Session.username();
    };
    vm.getUserId = function () {
      return Session.userId();
    };
    vm.logout = function () {
      AuthService.signOut();
    };
    vm.profilePage = function () {
      $location.path("/user/detail/" + vm.getUserId());
    };
    //appBaseUrl = "/grails-scaffolding";
    vm.alerts = [];
    $rootScope.$on(AUTH_EVENTS.notAuthenticated, function () {
      utilCustom.toaster($filter('translate')('login.blockedUser'));
      Session.destroy();
      $state.go("app.pages_auth_login");

    });
    $scope.$on(AUTH_EVENTS.notAuthorized, function () {
      $location.path('/404');
    });
    $scope.$on(AUTH_EVENTS.serverOff, function () {
      utilCustom.toaster($filter('translate')('backend.serverOff'));
    });
    $scope.$on(AUTH_EVENTS.permissionDenied, function () {
      Session.destroy();
      $state.go("app.pages_auth_login");
    });
    $scope.$on('tokenExpired', function () {
      //toaster.pop('error', $filter("translate")("com.ef.evr.label.user"), $filter("translate")("com.ef.evr.label.TokenExpired"));
      $state.go("app.pages_auth_login");
      Session.destroy();
      AuthService.hideSideMenu();

    });
    $scope.$on(AUTH_EVENTS.ccxNotResponding,function(){
      utilCustom.toaster($filter('translate')('backend.ccxNotResponding'));
    });
    $scope.$on(AUTH_EVENTS.notFound, function () {
      $location.path('/404');
    });
    $scope.$on(AUTH_EVENTS.sessionTimeout, function () {
      $location.path('Auth/419');
    });
    $scope.$on(AUTH_EVENTS.internalError, function () {
      $location.path('Auth/500');
    });
    // var newLength = vm.alerts.length;
    vm.closeAlert = function (index) {
      vm.alerts.splice(index, 1);
    };
    vm.translateKey = function (value) {

      return $filter("translate")(value);
    };
    vm.changeLanguage = function (langKey) {
      $translate.use(langKey);
    };
    vm.currentLang = function () {
      return  $translate.use();
    };



  }
})();

