$templateCache.put("app/main/components/material-docs/partials/api/material.components.autocomplete/directive/mdAutocomplete.html","<div class=\"doc-content\"><header class=\"api-profile-header\"><h2 class=\"md-display-1\">{{currentDoc.name}} API Documentation</h2></header><div layout=\"row\" class=\"api-options-bar with-icon\"></div><div class=\"api-profile-description\"><p><code>&lt;md-autocomplete&gt;</code> is a special input component with a drop-down of all possible matches to a custom query. This component allows you to provide real-time suggestions as the user types in the input area.</p><p>To start, you will need to specify the required parameters and provide a template for your results. The content inside <code>md-autocomplete</code> will be treated as a template.</p><p>In more complex cases, you may want to include other content such as a message to display when no matches were found. You can do this by wrapping your template in <code>md-item-template</code> and adding a tag for <code>md-not-found</code>. An example of this is shown below.</p><h3 id=\"validation\">Validation</h3><p>You can use <code>ng-messages</code> to include validation the same way that you would normally validate; however, if you want to replicate a standard input with a floating label, you will have to do the following:</p><ul><li>Make sure that your template is wrapped in <code>md-item-template</code></li><li>Add your <code>ng-messages</code> code inside of <code>md-autocomplete</code></li><li>Add your validation properties to <code>md-autocomplete</code> (ie. <code>required</code>)</li><li>Add a <code>name</code> to <code>md-autocomplete</code> (to be used on the generated <code>input</code>)</li></ul><p>There is an example below of how this should look.</p></div><div><section class=\"api-section\"><h2 id=\"Usage\">Usage</h2><h3 id=\"basic-example\">Basic Example</h3><hljs lang=\"html\"><md-autocomplete md-selected-item=\"selectedItem\" md-search-text=\"searchText\" md-items=\"item in getMatches(searchText)\" md-item-text=\"item.display\"><span md-highlight-text=\"searchText\">{{item.display}}</span></md-autocomplete></hljs><h3 id=\"example-with-not-found-message\">Example with &quot;not found&quot; message</h3><hljs lang=\"html\"><md-autocomplete md-selected-item=\"selectedItem\" md-search-text=\"searchText\" md-items=\"item in getMatches(searchText)\" md-item-text=\"item.display\"><md-item-template><span md-highlight-text=\"searchText\">{{item.display}}</span></md-item-template><md-not-found>No matches found.</md-not-found></md-autocomplete></hljs><p>In this example, our code utilizes <code>md-item-template</code> and <code>md-not-found</code> to specify the different parts that make up our component.</p><h3 id=\"example-with-validation\">Example with validation</h3><hljs lang=\"html\"><form name=\"autocompleteForm\"><md-autocomplete required=\"\" md-input-name=\"autocomplete\" md-selected-item=\"selectedItem\" md-search-text=\"searchText\" md-items=\"item in getMatches(searchText)\" md-item-text=\"item.display\"><md-item-template><span md-highlight-text=\"searchText\">{{item.display}}</span></md-item-template><div ng-messages=\"autocompleteForm.autocomplete.$error\"><div ng-message=\"required\">This field is required</div></div></md-autocomplete></form></hljs><p>In this example, our code utilizes <code>md-item-template</code> and <code>md-not-found</code> to specify the different parts that make up our component.</p></section><div class=\"api-param-section\"><h2>Attributes</h2><div class=\"api-param-table\"><table class=\"md-api-table\"><thead><tr><th>Parameter</th><th>Type</th><th>Description</th></tr></thead><tbody><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\"><b>* md-items</b> <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-expression\">expression</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-expression\">expression</code></td><td class=\"description\"><p>An expression in the format of <code>item in items</code> to iterate over matches for your search.</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">md-selected-item-change <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-expression\">expression</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-expression\">expression</code></td><td class=\"description\"><p>An expression to be run each time a new item is selected</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">md-search-text-change <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-expression\">expression</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-expression\">expression</code></td><td class=\"description\"><p>An expression to be run each time the search text updates</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">md-search-text <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-expression\">expression</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-expression\">expression</code></td><td class=\"description\"><p>A model to bind the search query text to</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">md-selected-item <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-object\">object</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-object\">object</code></td><td class=\"description\"><p>A model to bind the selected item to</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">md-item-text <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-expression\">expression</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-expression\">expression</code></td><td class=\"description\"><p>An expression that will convert your object to a single string.</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">placeholder <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-string\">string</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-string\">string</code></td><td class=\"description\"><p>Placeholder text that will be forwarded to the input.</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">md-no-cache <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-boolean\">boolean</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-boolean\">boolean</code></td><td class=\"description\"><p>Disables the internal caching that happens in autocomplete</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">ng-disabled <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-boolean\">boolean</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-boolean\">boolean</code></td><td class=\"description\"><p>Determines whether or not to disable the input field</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">md-min-length <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-number\">number</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-number\">number</code></td><td class=\"description\"><p>Specifies the minimum length of text before autocomplete will make suggestions</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">md-delay <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-number\">number</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-number\">number</code></td><td class=\"description\"><p>Specifies the amount of time (in milliseconds) to wait before looking for results</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">md-autofocus <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-boolean\">boolean</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-boolean\">boolean</code></td><td class=\"description\"><p>If true, will immediately focus the input element</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">md-autoselect <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-boolean\">boolean</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-boolean\">boolean</code></td><td class=\"description\"><p>If true, the first item will be selected by default</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">md-menu-class <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-string\">string</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-string\">string</code></td><td class=\"description\"><p>This will be applied to the dropdown menu for styling</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">md-floating-label <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-string\">string</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-string\">string</code></td><td class=\"description\"><p>This will add a floating label to autocomplete and wrap it in <code>md-input-container</code></p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">md-input-name <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-string\">string</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-string\">string</code></td><td class=\"description\"><p>The name attribute given to the input element to be used with FormController</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">md-input-id <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-string\">string</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-string\">string</code></td><td class=\"description\"><p>An ID to be added to the input element</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">md-input-minlength <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-number\">number</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-number\">number</code></td><td class=\"description\"><p>The minimum length for the input&#39;s value for validation</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">md-input-maxlength <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-number\">number</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-number\">number</code></td><td class=\"description\"><p>The maximum length for the input&#39;s value for validation</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">md-select-on-match <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-boolean\">boolean</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-boolean\">boolean</code></td><td class=\"description\"><p>When set, autocomplete will automatically select exact the item if the search text is an exact match</p></td></tr></tbody></table></div></div></div></div>");
$templateCache.put("app/main/components/material-docs/partials/api/material.components.autocomplete/directive/mdHighlightText.html","<div class=\"doc-content\"><header class=\"api-profile-header\"><h2 class=\"md-display-1\">{{currentDoc.name}} API Documentation</h2></header><div layout=\"row\" class=\"api-options-bar with-icon\"></div><div class=\"api-profile-description\"><p>The <code>md-highlight-text</code> directive allows you to specify text that should be highlighted within an element. Highlighted text will be wrapped in <code>&lt;span class=&quot;highlight&quot;&gt;&lt;/span&gt;</code> which can be styled through CSS. Please note that child elements may not be used with this directive.</p></div><div><section class=\"api-section\"><h2 id=\"Usage\">Usage</h2><hljs lang=\"html\"><input placeholder=\"Enter a search term...\" ng-model=\"searchTerm\" type=\"text\"><ul><li ng-repeat=\"result in results\" md-highlight-text=\"searchTerm\">{{result.text}}</li></ul></hljs></section><div class=\"api-param-section\"><h2>Attributes</h2><div class=\"api-param-table\"><table class=\"md-api-table\"><thead><tr><th>Parameter</th><th>Type</th><th>Description</th></tr></thead><tbody><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\"><b>* md-highlight-text</b> <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-string\">string</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-string\">string</code></td><td class=\"description\"><p>A model to be searched for</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">md-highlight-flags <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-string\">string</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-string\">string</code></td><td class=\"description\"><p>A list of flags (loosely based on JavaScript RexExp flags).</p><h4 id=\"-supported-flags-\"><strong>Supported flags</strong>:</h4><ul><li><code>g</code>: Find all matches within the provided text</li><li><code>i</code>: Ignore case when searching for matches</li><li><code>$</code>: Only match if the text ends with the search term</li><li><code>^</code>: Only match if the text begins with the search term</li></ul></td></tr></tbody></table></div></div></div></div>");
$templateCache.put("app/main/components/material-docs/partials/api/material.components.backdrop/directive/mdBackdrop.html","<div class=\"doc-content\"><header class=\"api-profile-header\"><h2 class=\"md-display-1\">{{currentDoc.name}} API Documentation</h2></header><div layout=\"row\" class=\"api-options-bar with-icon\"></div><div class=\"api-profile-description\"><p><code>&lt;md-backdrop&gt;</code> is a backdrop element used by other components, such as dialog and bottom sheet. Apply class <code>opaque</code> to make the backdrop use the theme backdrop color.</p></div><div><section class=\"api-section\"><h2 id=\"Usage\">Usage</h2><ul><li>as element:<pre><code>&lt;md-backdrop&gt;&#13;&#10;...&#13;&#10;&lt;/md-backdrop&gt;&#13;</code></pre></li></ul></section></div></div>");
$templateCache.put("app/main/components/material-docs/partials/api/material.components.bottomSheet/service/$mdBottomSheet.html","<div class=\"doc-content\"><header class=\"api-profile-header\"><h2 class=\"md-display-1\">{{currentDoc.name}} API Documentation</h2></header><div layout=\"row\" class=\"api-options-bar with-icon\"></div><div class=\"api-profile-description\"><p><code>$mdBottomSheet</code> opens a bottom sheet over the app and provides a simple promise API.</p><h2 id=\"restrictions\">Restrictions</h2><ul><li>The bottom sheet&#39;s template must have an outer <code>&lt;md-bottom-sheet&gt;</code> element.</li><li>Add the <code>md-grid</code> class to the bottom sheet for a grid layout.</li><li>Add the <code>md-list</code> class to the bottom sheet for a list layout.</li></ul></div><div><section class=\"api-section\"><h2 id=\"Usage\">Usage</h2><hljs lang=\"html\"><div ng-controller=\"MyController\"><md-button ng-click=\"openBottomSheet()\">Open a Bottom Sheet!</md-button></div></hljs><hljs lang=\"js\">var app = angular.module(&#39;app&#39;, [&#39;ngMaterial&#39;]); app.controller(&#39;MyController&#39;, function($scope, $mdBottomSheet) { $scope.openBottomSheet = function() { $mdBottomSheet.show({ template: &#39;<md-bottom-sheet>Hello!</md-bottom-sheet>&#39; }); }; });</hljs></section><section class=\"api-section\"><h2>Methods</h2><br><ul class=\"methods\"><li id=\"show\"><h3 class=\"method-function-syntax\"><code class=\"method-function-syntax\">$mdBottomSheet.show(options);\r\n</code></h3><div class=\"service-desc\"><p>Show a bottom sheet with the specified options.</p></div><div class=\"method-param-table\"><table class=\"md-api-table\"><thead><tr><th>Parameter</th><th>Type</th><th>Description</th></tr></thead><tbody><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\"><b>* options</b> <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-object\">object</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-object\">object</code></td><td class=\"description\"><p>An options object, with the following properties:</p><ul><li><code>templateUrl</code> - <code>{string=}</code>: The url of an html template file that will be used as the content of the bottom sheet. Restrictions: the template must have an outer <code>md-bottom-sheet</code> element.</li><li><code>template</code> - <code>{string=}</code>: Same as templateUrl, except this is an actual template string.</li><li><code>scope</code> - <code>{object=}</code>: the scope to link the template / controller to. If none is specified, it will create a new child scope. This scope will be destroyed when the bottom sheet is removed unless <code>preserveScope</code> is set to true.</li><li><code>preserveScope</code> - <code>{boolean=}</code>: whether to preserve the scope when the element is removed. Default is false</li><li><code>controller</code> - <code>{string=}</code>: The controller to associate with this bottom sheet.</li><li><code>locals</code> - <code>{string=}</code>: An object containing key/value pairs. The keys will be used as names of values to inject into the controller. For example, <code>locals: {three: 3}</code> would inject <code>three</code> into the controller with the value of 3.</li><li><code>clickOutsideToClose</code> - <code>{boolean=}</code>: Whether the user can click outside the bottom sheet to close it. Default true.</li><li><code>escapeToClose</code> - <code>{boolean=}</code>: Whether the user can press escape to close the bottom sheet. Default true.</li><li><code>resolve</code> - <code>{object=}</code>: Similar to locals, except it takes promises as values and the bottom sheet will not open until the promises resolve.</li><li><code>controllerAs</code> - <code>{string=}</code>: An alias to assign the controller to on the scope.</li><li><code>parent</code> - <code>{element=}</code>: The element to append the bottom sheet to. The <code>parent</code> may be a <code>function</code>, <code>string</code>, <code>object</code>, or null. Defaults to appending to the body of the root element (or the root element) of the application. e.g. angular.element(document.getElementById(&#39;content&#39;)) or &quot;#content&quot;</li><li><code>disableParentScroll</code> - <code>{boolean=}</code>: Whether to disable scrolling while the bottom sheet is open. Default true.</li></ul></td></tr></tbody></table><table class=\"md-api-table\"><thead><tr><th>Returns</th><th>Description</th></tr></thead><tbody><tr><td><code class=\"api-type label type-hint type-hint-promise\">promise</code></td><td class=\"description\"><p>A promise that can be resolved with <code>$mdBottomSheet.hide()</code> or rejected with <code>$mdBottomSheet.cancel()</code>.</p></td></tr></tbody></table></div></li><li id=\"hide\"><h3 class=\"method-function-syntax\"><code class=\"method-function-syntax\">$mdBottomSheet.hide([response]);\r\n</code></h3><div class=\"service-desc\"><p>Hide the existing bottom sheet and resolve the promise returned from <code>$mdBottomSheet.show()</code>. This call will close the most recently opened/current bottomsheet (if any).</p></div><div class=\"method-param-table\"><table class=\"md-api-table\"><thead><tr><th>Parameter</th><th>Type</th><th>Description</th></tr></thead><tbody><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">response <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-object\">*</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-object\">*</code></td><td class=\"description\"><p>An argument for the resolved promise.</p></td></tr></tbody></table></div></li><li id=\"cancel\"><h3 class=\"method-function-syntax\"><code class=\"method-function-syntax\">$mdBottomSheet.cancel([response]);\r\n</code></h3><div class=\"service-desc\"><p>Hide the existing bottom sheet and reject the promise returned from <code>$mdBottomSheet.show()</code>.</p></div><div class=\"method-param-table\"><table class=\"md-api-table\"><thead><tr><th>Parameter</th><th>Type</th><th>Description</th></tr></thead><tbody><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">response <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-object\">*</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-object\">*</code></td><td class=\"description\"><p>An argument for the rejected promise.</p></td></tr></tbody></table></div></li></ul></section></div></div>");
$templateCache.put("app/main/components/material-docs/partials/api/material.components.button/directive/mdButton.html","<div class=\"doc-content\"><header class=\"api-profile-header\"><h2 class=\"md-display-1\">{{currentDoc.name}} API Documentation</h2></header><div layout=\"row\" class=\"api-options-bar with-icon\"></div><div class=\"api-profile-description\"><p><code>&lt;md-button&gt;</code> is a button directive with optional ink ripples (default enabled).</p><p>If you supply a <code>href</code> or <code>ng-href</code> attribute, it will become an <code>&lt;a&gt;</code> element. Otherwise, it will become a <code>&lt;button&gt;</code> element. As per the <a href=\"http://www.google.com/design/spec/style/color.html#color-ui-color-application\">Material Design specifications</a> the FAB button background is filled with the accent color [by default]. The primary color palette may be used with the <code>md-primary</code> class.</p></div><div><section class=\"api-section\"><h2 id=\"Usage\">Usage</h2><p>Regular buttons:</p><hljs lang=\"html\"><md-button>Flat Button</md-button><md-button href=\"http://google.com\">Flat link</md-button><md-button class=\"md-raised\">Raised Button</md-button><md-button ng-disabled=\"true\">Disabled Button</md-button><md-button><md-icon md-svg-src=\"your/icon.svg\"></md-icon>Register Now</md-button></hljs><p>FAB buttons:</p><hljs lang=\"html\"><md-button class=\"md-fab\" aria-label=\"FAB\"><md-icon md-svg-src=\"your/icon.svg\"></md-icon></md-button><md-button class=\"md-fab md-mini\" aria-label=\"Mini FAB\"><md-icon md-svg-src=\"your/icon.svg\"></md-icon></md-button><md-button class=\"md-icon-button\" aria-label=\"Custom Icon Button\"><md-icon md-svg-icon=\"path/to/your.svg\"></md-icon></md-button></hljs></section><div class=\"api-param-section\"><h2>Attributes</h2><div class=\"api-param-table\"><table class=\"md-api-table\"><thead><tr><th>Parameter</th><th>Type</th><th>Description</th></tr></thead><tbody><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">md-no-ink <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-boolean\">boolean</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-boolean\">boolean</code></td><td class=\"description\"><p>If present, disable ripple ink effects.</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">ng-disabled <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-expression\">expression</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-expression\">expression</code></td><td class=\"description\"><p>En/Disable based on the expression</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">md-ripple-size <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-string\">string</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-string\">string</code></td><td class=\"description\"><p>Overrides the default ripple size logic. Options: <code>full</code>, <code>partial</code>, <code>auto</code></p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">aria-label <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-string\">string</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-string\">string</code></td><td class=\"description\"><p>Adds alternative text to button for accessibility, useful for icon buttons. If no default text is found, a warning will be logged.</p></td></tr></tbody></table></div></div></div></div>");
$templateCache.put("app/main/components/material-docs/partials/api/material.components.card/directive/mdCard.html","<div class=\"doc-content\"><header class=\"api-profile-header\"><h2 class=\"md-display-1\">{{currentDoc.name}} API Documentation</h2></header><div layout=\"row\" class=\"api-options-bar with-icon\"></div><div class=\"api-profile-description\"><p>The <code>&lt;md-card&gt;</code> directive is a container element used within <code>&lt;md-content&gt;</code> containers.</p><p>An image included as a direct descendant will fill the card&#39;s width, while the <code>&lt;md-card-content&gt;</code> container will wrap text content and provide padding. An <code>&lt;md-card-footer&gt;</code> element can be optionally included to put content flush against the bottom edge of the card.</p><p>Action buttons can be included in an <code>&lt;md-card-actions&gt;</code> element, similar to <code>&lt;md-dialog-actions&gt;</code>. You can then position buttons using layout attributes.</p><p>Card is built with:</p><ul><li><code>&lt;md-card-header&gt;</code> - Header for the card, holds avatar, text and squared image<ul><li><code>&lt;md-card-avatar&gt;</code> - Card avatar<ul><li><code>md-user-avatar</code> - Class for user image</li><li><code>&lt;md-icon&gt;</code></li></ul></li><li><code>&lt;md-card-header-text&gt;</code> - Contains elements for the card description<ul><li><code>md-title</code> - Class for the card title</li><li><code>md-subhead</code> - Class for the card sub header</li></ul></li></ul></li><li><code>&lt;img&gt;</code> - Image for the card</li><li><code>&lt;md-card-title&gt;</code> - Card content title<ul><li><code>&lt;md-card-title-text&gt;</code><ul><li><code>md-headline</code> - Class for the card content title</li><li><code>md-subhead</code> - Class for the card content sub header</li></ul></li><li><code>&lt;md-card-title-media&gt;</code> - Squared image within the title<ul><li><code>md-media-sm</code> - Class for small image</li><li><code>md-media-md</code> - Class for medium image</li><li><code>md-media-lg</code> - Class for large image</li></ul></li></ul></li><li><code>&lt;md-card-content&gt;</code> - Card content<ul><li><code>md-media-xl</code> - Class for extra large image</li></ul></li><li><code>&lt;md-card-actions&gt;</code> - Card actions<ul><li><code>&lt;md-card-icon-actions&gt;</code> - Icon actions</li></ul></li></ul><p>Cards have constant width and variable heights; where the maximum height is limited to what can fit within a single view on a platform, but it can temporarily expand as needed.</p></div><div><section class=\"api-section\"><h2 id=\"Usage\">Usage</h2><h3 id=\"card-with-optional-footer\">Card with optional footer</h3><hljs lang=\"html\"><md-card><img src=\"card-image.png\" class=\"md-card-image\" alt=\"image caption\"><md-card-content><h2>Card headline</h2><p>Card content</p></md-card-content><md-card-footer>Card footer</md-card-footer></md-card></hljs><h3 id=\"card-with-actions\">Card with actions</h3><hljs lang=\"html\"><md-card><img src=\"card-image.png\" class=\"md-card-image\" alt=\"image caption\"><md-card-content><h2>Card headline</h2><p>Card content</p></md-card-content><md-card-actions layout=\"row\" layout-align=\"end center\"><md-button>Action 1</md-button><md-button>Action 2</md-button></md-card-actions></md-card></hljs><h3 id=\"card-with-header-image-title-actions-and-content\">Card with header, image, title actions and content</h3><hljs lang=\"html\"><md-card><md-card-header><md-card-avatar><img class=\"md-user-avatar\" src=\"avatar.png\"></md-card-avatar><md-card-header-text><span class=\"md-title\">Title</span> <span class=\"md-subhead\">Sub header</span></md-card-header-text></md-card-header><img ng-src=\"card-image.png\" class=\"md-card-image\" alt=\"image caption\"><md-card-title><md-card-title-text><span class=\"md-headline\">Card headline</span> <span class=\"md-subhead\">Card subheader</span></md-card-title-text></md-card-title><md-card-actions layout=\"row\" layout-align=\"start center\"><md-button>Action 1</md-button><md-button>Action 2</md-button><md-card-icon-actions><md-button class=\"md-icon-button\" aria-label=\"icon\"><md-icon md-svg-icon=\"icon\"></md-icon></md-button></md-card-icon-actions></md-card-actions><md-card-content><p>Card content</p></md-card-content></md-card></hljs></section></div></div>");
$templateCache.put("app/main/components/material-docs/partials/api/material.components.checkbox/directive/mdCheckbox.html","<div class=\"doc-content\"><header class=\"api-profile-header\"><h2 class=\"md-display-1\">{{currentDoc.name}} API Documentation</h2></header><div layout=\"row\" class=\"api-options-bar with-icon\"></div><div class=\"api-profile-description\"><p>The checkbox directive is used like the normal <a href=\"https://docs.angularjs.org/api/ng/input/input%5Bcheckbox%5D\">angular checkbox</a>.</p><p>As per the <a href=\"http://www.google.com/design/spec/style/color.html#color-ui-color-application\">material design spec</a> the checkbox is in the accent color by default. The primary color palette may be used with the <code>md-primary</code> class.</p></div><div><section class=\"api-section\"><h2 id=\"Usage\">Usage</h2><hljs lang=\"html\"><md-checkbox ng-model=\"isChecked\" aria-label=\"Finished?\">Finished ?</md-checkbox><md-checkbox md-no-ink=\"\" ng-model=\"hasInk\" aria-label=\"No Ink Effects\">No Ink Effects</md-checkbox><md-checkbox ng-disabled=\"true\" ng-model=\"isDisabled\" aria-label=\"Disabled\">Disabled</md-checkbox></hljs></section><div class=\"api-param-section\"><h2>Attributes</h2><div class=\"api-param-table\"><table class=\"md-api-table\"><thead><tr><th>Parameter</th><th>Type</th><th>Description</th></tr></thead><tbody><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\"><b>* ng-model</b> <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-string\">string</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-string\">string</code></td><td class=\"description\"><p>Assignable angular expression to data-bind to.</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">name <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-string\">string</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-string\">string</code></td><td class=\"description\"><p>Property name of the form under which the control is published.</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">ng-true-value <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-expression\">expression</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-expression\">expression</code></td><td class=\"description\"><p>The value to which the expression should be set when selected.</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">ng-false-value <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-expression\">expression</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-expression\">expression</code></td><td class=\"description\"><p>The value to which the expression should be set when not selected.</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">ng-change <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-string\">string</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-string\">string</code></td><td class=\"description\"><p>Angular expression to be executed when input changes due to user interaction with the input element.</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">md-no-ink <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-boolean\">boolean</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-boolean\">boolean</code></td><td class=\"description\"><p>Use of attribute indicates use of ripple ink effects</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">aria-label <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-string\">string</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-string\">string</code></td><td class=\"description\"><p>Adds label to checkbox for accessibility. Defaults to checkbox&#39;s text. If no default text is found, a warning will be logged.</p></td></tr></tbody></table></div></div></div></div>");
$templateCache.put("app/main/components/material-docs/partials/api/material.components.chips/directive/mdChip.html","<div class=\"doc-content\"><header class=\"api-profile-header\"><h2 class=\"md-display-1\">{{currentDoc.name}} API Documentation</h2></header><div layout=\"row\" class=\"api-options-bar with-icon\"></div><div class=\"api-profile-description\"><p><code>&lt;md-chip&gt;</code> is a component used within <code>&lt;md-chips&gt;</code> and is responsible for rendering individual chips.</p></div><div><section class=\"api-section\"><h2 id=\"Usage\">Usage</h2><hljs lang=\"html\"><md-chip>{{$chip}}</md-chip></hljs></section></div></div>");
$templateCache.put("app/main/components/material-docs/partials/api/material.components.chips/directive/mdChipRemove.html","<div class=\"doc-content\"><header class=\"api-profile-header\"><h2 class=\"md-display-1\">{{currentDoc.name}} API Documentation</h2></header><div layout=\"row\" class=\"api-options-bar with-icon\"></div><div class=\"api-profile-description\"><p><code>&lt;md-chip-remove&gt;</code> Designates an element to be used as the delete button for a chip. This element is passed as a child of the <code>md-chips</code> element.</p></div><div><section class=\"api-section\"><h2 id=\"Usage\">Usage</h2><hljs lang=\"html\"><md-chips><button md-chip-remove=\"\">DEL</button></md-chips></hljs></section></div></div>");
$templateCache.put("app/main/components/material-docs/partials/api/material.components.chips/directive/mdChips.html","<div class=\"doc-content\"><header class=\"api-profile-header\"><h2 class=\"md-display-1\">{{currentDoc.name}} API Documentation</h2></header><div layout=\"row\" class=\"api-options-bar with-icon\"></div><div class=\"api-profile-description\"><p><code>&lt;md-chips&gt;</code> is an input component for building lists of strings or objects. The list items are displayed as &#39;chips&#39;. This component can make use of an <code>&lt;input&gt;</code> element or an <code>&lt;md-autocomplete&gt;</code> element.</p><h3 id=\"custom-templates\">Custom templates</h3><p>A custom template may be provided to render the content of each chip. This is achieved by specifying an <code>&lt;md-chip-template&gt;</code> element containing the custom content as a child of <code>&lt;md-chips&gt;</code>.</p><p>Note: Any attributes on <code>&lt;md-chip-template&gt;</code> will be dropped as only the innerHTML is used for the chip template. The variables <code>$chip</code> and <code>$index</code> are available in the scope of <code>&lt;md-chip-template&gt;</code>, representing the chip object and its index in the list of chips, respectively. To override the chip delete control, include an element (ideally a button) with the attribute <code>md-chip-remove</code>. A click listener to remove the chip will be added automatically. The element is also placed as a sibling to the chip content (on which there are also click listeners) to avoid a nested ng-click situation.</p><p></p><h3>Pending Features</h3><p></p><ul style=\"padding-left:20px;\"><ul>Style<li>Colours for hover, press states (ripple?).</li></ul><ul>Validation<li>allow a validation callback</li><li>hilighting style for invalid chips</li></ul><ul>Item mutation<li>Support <code>&lt;md-chip-edit&gt;</code> template, show/hide the edit element on tap/click? double tap/double click?</li></ul><ul>Truncation and Disambiguation (?)<li>Truncate chip text where possible, but do not truncate entries such that two are indistinguishable.</li></ul><ul>Drag and Drop<li>Drag and drop chips between related <code>&lt;md-chips&gt;</code> elements.</li></ul></ul><p><span style=\"font-size:.8em;text-align:center\">Warning: This component is a WORK IN PROGRESS. If you use it now, it will probably break on you in the future.</span></p></div><div><section class=\"api-section\"><h2 id=\"Usage\">Usage</h2><hljs lang=\"html\"><md-chips ng-model=\"myItems\" placeholder=\"Add an item\" readonly=\"isReadOnly\"></md-chips></hljs></section><div class=\"api-param-section\"><h2>Attributes</h2><div class=\"api-param-table\"><table class=\"md-api-table\"><thead><tr><th>Parameter</th><th>Type</th><th>Description</th></tr></thead><tbody><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\"><b>* ng-model</b> <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-string\">string</code><code class=\"api-type label type-hint type-hint-object\">object</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-string\">string</code><code class=\"api-type label type-hint type-hint-object\">object</code></td><td class=\"description\"><p>A model to bind the list of items to</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\"><b>* md-transform-chip</b> <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-expression\">expression</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-expression\">expression</code></td><td class=\"description\"><p>An expression of form <code>myFunction($chip)</code> that when called expects one of the following return values:</p><ul><li>an object representing the <code>$chip</code> input string</li><li><code>undefined</code> to simply add the <code>$chip</code> input string, or</li><li><code>null</code> to prevent the chip from being appended</li></ul></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\"><b>* md-require-match</b> <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-boolean\">boolean</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-boolean\">boolean</code></td><td class=\"description\"><p>If true, and the chips template contains an autocomplete, only allow selection of pre-defined chips (i.e. you cannot add new ones).</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">placeholder <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-string\">string</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-string\">string</code></td><td class=\"description\"><p>Placeholder text that will be forwarded to the input.</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">secondary-placeholder <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-string\">string</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-string\">string</code></td><td class=\"description\"><p>Placeholder text that will be forwarded to the input, displayed when there is at least on item in the list</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">readonly <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-boolean\">boolean</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-boolean\">boolean</code></td><td class=\"description\"><p>Disables list manipulation (deleting or adding list items), hiding the input and delete buttons</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">md-on-add <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-expression\">expression</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-expression\">expression</code></td><td class=\"description\"><p>An expression which will be called when a chip has been added.</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">md-on-remove <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-expression\">expression</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-expression\">expression</code></td><td class=\"description\"><p>An expression which will be called when a chip has been removed.</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">md-on-select <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-expression\">expression</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-expression\">expression</code></td><td class=\"description\"><p>An expression which will be called when a chip is selected.</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">delete-hint <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-string\">string</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-string\">string</code></td><td class=\"description\"><p>A string read by screen readers instructing users that pressing the delete key will remove the chip.</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">delete-button-label <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-string\">string</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-string\">string</code></td><td class=\"description\"><p>A label for the delete button. Also hidden and read by screen readers.</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">md-separator-keys <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-expression\">expression</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-expression\">expression</code></td><td class=\"description\"><p>An array of key codes used to separate chips.</p></td></tr></tbody></table></div></div></div></div>");
$templateCache.put("app/main/components/material-docs/partials/api/material.components.chips/directive/mdContactChips.html","<div class=\"doc-content\"><header class=\"api-profile-header\"><h2 class=\"md-display-1\">{{currentDoc.name}} API Documentation</h2></header><div layout=\"row\" class=\"api-options-bar with-icon\"></div><div class=\"api-profile-description\"><p><code>&lt;md-contact-chips&gt;</code> is an input component based on <code>md-chips</code> and makes use of an <code>md-autocomplete</code> element. The component allows the caller to supply a query expression which returns a list of possible contacts. The user can select one of these and add it to the list of chips.</p><p>You may also use the <code>md-highlight-text</code> directive along with it&#39;s parameters to control the appearance of the matched text inside of the contacts&#39; autocomplete popup.</p></div><div><section class=\"api-section\"><h2 id=\"Usage\">Usage</h2><hljs lang=\"html\"><md-contact-chips ng-model=\"ctrl.contacts\" md-contacts=\"ctrl.querySearch($query)\" md-contact-name=\"name\" md-contact-image=\"image\" md-contact-email=\"email\" placeholder=\"To\"></md-contact-chips></hljs></section><div class=\"api-param-section\"><h2>Attributes</h2><div class=\"api-param-table\"><table class=\"md-api-table\"><thead><tr><th>Parameter</th><th>Type</th><th>Description</th></tr></thead><tbody><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\"><b>* ng-model</b> <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-string\">string</code><code class=\"api-type label type-hint type-hint-object\">object</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-string\">string</code><code class=\"api-type label type-hint type-hint-object\">object</code></td><td class=\"description\"><p>A model to bind the list of items to</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\"><b>* md-contacts</b> <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-expression\">expression</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-expression\">expression</code></td><td class=\"description\"><p>An expression expected to return contacts matching the search test, <code>$query</code>.</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\"><b>* md-contact-name</b> <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-string\">string</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-string\">string</code></td><td class=\"description\"><p>The field name of the contact object representing the contact&#39;s name.</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\"><b>* md-contact-email</b> <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-string\">string</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-string\">string</code></td><td class=\"description\"><p>The field name of the contact object representing the contact&#39;s email address.</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\"><b>* md-contact-image</b> <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-string\">string</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-string\">string</code></td><td class=\"description\"><p>The field name of the contact object representing the contact&#39;s image.</p><p>// The following attribute has been removed but may come back.</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">placeholder <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-string\">string</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-string\">string</code></td><td class=\"description\"><p>Placeholder text that will be forwarded to the input.</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">secondary-placeholder <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-string\">string</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-string\">string</code></td><td class=\"description\"><p>Placeholder text that will be forwarded to the input, displayed when there is at least on item in the list</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">filter-selected <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-expression\">expression</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-expression\">expression</code></td><td class=\"description\"><p>Whether to filter selected contacts from the list of suggestions shown in the autocomplete.</p></td></tr></tbody></table></div></div></div></div>");
$templateCache.put("app/main/components/material-docs/partials/api/material.components.content/directive/mdContent.html","<div class=\"doc-content\"><header class=\"api-profile-header\"><h2 class=\"md-display-1\">{{currentDoc.name}} API Documentation</h2></header><div layout=\"row\" class=\"api-options-bar with-icon\"></div><div class=\"api-profile-description\"><p>The <code>&lt;md-content&gt;</code> directive is a container element useful for scrollable content</p></div><div><section class=\"api-section\"><h2 id=\"Usage\">Usage</h2><ul><li>Add the <code>[layout-padding]</code> attribute to make the content padded.</li></ul><hljs lang=\"html\"><md-content layout-padding=\"\">Lorem ipsum dolor sit amet, ne quod novum mei.</md-content></hljs></section></div></div>");
$templateCache.put("app/main/components/material-docs/partials/api/material.components.datepicker/directive/mdDatepicker.html","<div class=\"doc-content\"><header class=\"api-profile-header\"><h2 class=\"md-display-1\">{{currentDoc.name}} API Documentation</h2></header><div layout=\"row\" class=\"api-options-bar with-icon\"></div><div class=\"api-profile-description\"><p><code>&lt;md-datepicker&gt;</code> is a component used to select a single date. For information on how to configure internationalization for the date picker, see <code>$mdDateLocaleProvider</code>.</p><p>This component supports <a href=\"https://docs.angularjs.org/api/ngMessages/directive/ngMessages\">ngMessages</a>. Supported attributes are:</p><ul><li><code>required</code>: whether a required date is not set.</li><li><code>mindate</code>: whether the selected date is before the minimum allowed date.</li><li><code>maxdate</code>: whether the selected date is after the maximum allowed date.</li></ul></div><div><section class=\"api-section\"><h2 id=\"Usage\">Usage</h2><hljs lang=\"html\"><md-datepicker ng-model=\"birthday\"></md-datepicker></hljs></section><div class=\"api-param-section\"><h2>Attributes</h2><div class=\"api-param-table\"><table class=\"md-api-table\"><thead><tr><th>Parameter</th><th>Type</th><th>Description</th></tr></thead><tbody><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\"><b>* ng-model</b> <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-date\">Date</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-date\">Date</code></td><td class=\"description\"><p>The component&#39;s model. Expects a JavaScript Date object.</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">ng-change <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-expression\">expression</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-expression\">expression</code></td><td class=\"description\"><p>Expression evaluated when the model value changes.</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">md-min-date <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-date\">Date</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-date\">Date</code></td><td class=\"description\"><p>Expression representing a min date (inclusive).</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">md-max-date <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-date\">Date</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-date\">Date</code></td><td class=\"description\"><p>Expression representing a max date (inclusive).</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">md-date-filter <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-function\">function(Date): boolean</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-function\">function(Date): boolean</code></td><td class=\"description\"><p>Function expecting a date and returning a boolean whether it can be selected or not.</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">md-placeholder <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-string\">String</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-string\">String</code></td><td class=\"description\"><p>The date input placeholder value.</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">ng-disabled <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-boolean\">boolean</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-boolean\">boolean</code></td><td class=\"description\"><p>Whether the datepicker is disabled.</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">ng-required <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-boolean\">boolean</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-boolean\">boolean</code></td><td class=\"description\"><p>Whether a value is required for the datepicker.</p></td></tr></tbody></table></div></div></div></div>");
$templateCache.put("app/main/components/material-docs/partials/api/material.components.datepicker/service/$mdDateLocaleProvider.html","<div class=\"doc-content\"><header class=\"api-profile-header\"><h2 class=\"md-display-1\">{{currentDoc.name}} API Documentation</h2></header><div layout=\"row\" class=\"api-options-bar with-icon\"></div><div class=\"api-profile-description\"><p>The <code>$mdDateLocaleProvider</code> is the provider that creates the <code>$mdDateLocale</code> service. This provider that allows the user to specify messages, formatters, and parsers for date internationalization. The <code>$mdDateLocale</code> service itself is consumed by Angular Material components that that deal with dates.</p></div><div><section class=\"api-section\"><h2 id=\"Usage\">Usage</h2><hljs lang=\"js\">myAppModule.config(function($mdDateLocaleProvider) { // Example of a French localization. $mdDateLocaleProvider.months = [&#39;janvier&#39;, &#39;fvrier&#39;, &#39;mars&#39;, ...]; $mdDateLocaleProvider.shortMonths = [&#39;janv&#39;, &#39;fvr&#39;, &#39;mars&#39;, ...]; $mdDateLocaleProvider.days = [&#39;dimanche&#39;, &#39;lundi&#39;, &#39;mardi&#39;, ...]; $mdDateLocaleProvider.shortDays = [&#39;Di&#39;, &#39;Lu&#39;, &#39;Ma&#39;, ...]; // Can change week display to start on Monday. $mdDateLocaleProvider.firstDayOfWeek = 1; // Optional. $mdDateLocaleProvider.dates = [1, 2, 3, 4, 5, 6, ...]; // Example uses moment.js to parse and format dates. $mdDateLocaleProvider.parseDate = function(dateString) { var m = moment(dateString, &#39;L&#39;, true); return m.isValid() ? m.toDate() : new Date(NaN); }; $mdDateLocaleProvider.formatDate = function(date) { return moment(date).format(&#39;L&#39;); }; $mdDateLocaleProvider.monthHeaderFormatter = function(date) { return myShortMonths[date.getMonth()] + &#39; &#39; + date.getFullYear(); }; // In addition to date display, date components also need localized messages // for aria-labels for screen-reader users. $mdDateLocaleProvider.weekNumberFormatter = function(weekNumber) { return &#39;Semaine &#39; + weekNumber; }; $mdDateLocaleProvider.msgCalendar = &#39;Calendrier&#39;; $mdDateLocaleProvider.msgOpenCalendar = &#39;Ouvrir le calendrier&#39;; });</hljs></section><section class=\"api-section\"><h2>Properties</h2><div class=\"api-param-table\"><table class=\"md-api-table\"><thead><tr><th>Property</th><th>Type</th><th>Description</th></tr></thead><tbody><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">months <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-array\">Array.&lt;string&gt;</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-array\">Array.&lt;string&gt;</code></td><td class=\"description\"><p>Array of month names (in order).</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">shortMonths <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-array\">Array.&lt;string&gt;</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-array\">Array.&lt;string&gt;</code></td><td class=\"description\"><p>Array of abbreviated month names.</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">days <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-array\">Array.&lt;string&gt;</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-array\">Array.&lt;string&gt;</code></td><td class=\"description\"><p>Array of the days of the week (in order).</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">shortDays <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-array\">Array.&lt;string&gt;</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-array\">Array.&lt;string&gt;</code></td><td class=\"description\"><p>Array of abbreviated dayes of the week.</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">dates <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-array\">Array.&lt;string&gt;</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-array\">Array.&lt;string&gt;</code></td><td class=\"description\"><p>Array of dates of the month. Only necessary for locales using a numeral system other than [1, 2, 3...].</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">firstDayOfWeek <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-array\">Array.&lt;string&gt;</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-array\">Array.&lt;string&gt;</code></td><td class=\"description\"><p>The first day of the week. Sunday = 0, Monday = 1, etc.</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">parseDate <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-function\">function(string): Date</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-function\">function(string): Date</code></td><td class=\"description\"><p>Function to parse a date object from a string.</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">formatDate <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-function\">function(Date): string</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-function\">function(Date): string</code></td><td class=\"description\"><p>Function to format a date object to a string.</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">monthHeaderFormatter <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-function\">function(Date): string</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-function\">function(Date): string</code></td><td class=\"description\"><p>Function that returns the label for a month given a date.</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">weekNumberFormatter <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-function\">function(number): string</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-function\">function(number): string</code></td><td class=\"description\"><p>Function that returns a label for a week given the week number.</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">msgCalendar <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-string\">string</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-string\">string</code></td><td class=\"description\"><p>Translation of the label &quot;Calendar&quot; for the current locale.</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">msgOpenCalendar <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-string\">string</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-string\">string</code></td><td class=\"description\"><p>Translation of the button label &quot;Open calendar&quot; for the current locale.</p></td></tr></tbody></table></div></section></div></div>");
$templateCache.put("app/main/components/material-docs/partials/api/material.components.dialog/directive/mdDialog.html","<div class=\"doc-content\"><header class=\"api-profile-header\"><h2 class=\"md-display-1\">{{currentDoc.name}} API Documentation</h2></header><div layout=\"row\" class=\"api-options-bar with-icon\"></div><div class=\"api-profile-description\"><p><code>&lt;md-dialog&gt;</code> - The dialog&#39;s template must be inside this element.</p><p>Inside, use an <code>&lt;md-dialog-content&gt;</code> element for the dialog&#39;s content, and use an <code>&lt;md-dialog-actions&gt;</code> element for the dialog&#39;s actions.</p><ul><li><h2 id=\"css\">CSS</h2></li><li><code>.md-dialog-content</code> - class that sets the padding on the content as the spec file</li></ul></div><div><section class=\"api-section\"><h2 id=\"Usage\">Usage</h2><h3 id=\"dialog-template\">Dialog template</h3><hljs lang=\"html\"><md-dialog aria-label=\"List dialog\"><md-dialog-content><md-list><md-list-item ng-repeat=\"item in items\"><p>Number {{item}}</p></md-list-item></md-list></md-dialog-content><md-dialog-actions><md-button ng-click=\"closeDialog()\" class=\"md-primary\">Close Dialog</md-button></md-dialog-actions></md-dialog></hljs></section></div></div>");
$templateCache.put("app/main/components/material-docs/partials/api/material.components.dialog/service/$mdDialog.html","<div class=\"doc-content\"><header class=\"api-profile-header\"><h2 class=\"md-display-1\">{{currentDoc.name}} API Documentation</h2></header><div layout=\"row\" class=\"api-options-bar with-icon\"></div><div class=\"api-profile-description\"><p><code>$mdDialog</code> opens a dialog over the app to inform users about critical information or require them to make decisions. There are two approaches for setup: a simple promise API and regular object syntax.</p><h2 id=\"restrictions\">Restrictions</h2><ul><li>The dialog is always given an isolate scope.</li><li>The dialog&#39;s template must have an outer <code>&lt;md-dialog&gt;</code> element. Inside, use an <code>&lt;md-dialog-content&gt;</code> element for the dialog&#39;s content, and use an <code>&lt;md-dialog-actions&gt;</code> element for the dialog&#39;s actions.</li><li>Dialogs must cover the entire application to keep interactions inside of them. Use the <code>parent</code> option to change where dialogs are appended.</li></ul><h2 id=\"sizing\">Sizing</h2><ul><li>Complex dialogs can be sized with <code>flex=&quot;percentage&quot;</code>, i.e. <code>flex=&quot;66&quot;</code>.</li><li>Default max-width is 80% of the <code>rootElement</code> or <code>parent</code>.</li></ul><h2 id=\"css\">CSS</h2><ul><li><code>.md-dialog-content</code> - class that sets the padding on the content as the spec file</li></ul></div><div><section class=\"api-section\"><h2 id=\"Usage\">Usage</h2><hljs lang=\"html\"><div ng-app=\"demoApp\" ng-controller=\"EmployeeController\"><div><md-button ng-click=\"showAlert()\" class=\"md-raised md-warn\">Employee Alert!</md-button></div><div><md-button ng-click=\"showDialog($event)\" class=\"md-raised\">Custom Dialog</md-button></div><div><md-button ng-click=\"closeAlert()\" ng-disabled=\"!hasAlert()\" class=\"md-raised\">Close Alert</md-button></div><div><md-button ng-click=\"showGreeting($event)\" class=\"md-raised md-primary\">Greet Employee</md-button></div></div></hljs><h3 id=\"javascript-object-syntax\">JavaScript: object syntax</h3><hljs lang=\"js\">(function(angular, undefined){ &quot;use strict&quot;; angular .module(&#39;demoApp&#39;, [&#39;ngMaterial&#39;]) .controller(&#39;AppCtrl&#39;, AppController); function AppController($scope, $mdDialog) { var alert; $scope.showAlert = showAlert; $scope.showDialog = showDialog; $scope.items = [1, 2, 3]; // Internal method function showAlert() { alert = $mdDialog.alert({ title: &#39;Attention&#39;, textContent: &#39;This is an example of how easy dialogs can be!&#39;, ok: &#39;Close&#39; }); $mdDialog .show( alert ) .finally(function() { alert = undefined; }); } function showDialog($event) { var parentEl = angular.element(document.body); $mdDialog.show({ parent: parentEl, targetEvent: $event, template: &#39;<md-dialog aria-label=\"List dialog\">&#39; + &#39;<md-dialog-content>&#39;+ &#39;<md-list>&#39;+ &#39;<md-list-item ng-repeat=\"item in items\">&#39;+ &#39;<p>Number {{item}}</p>&#39; + &#39; &#39;+ &#39;</md-list-item></md-list>&#39;+ &#39;</md-dialog-content>&#39; + &#39;<md-dialog-actions>&#39; + &#39;<md-button ng-click=\"closeDialog()\" class=\"md-primary\">&#39; + &#39; Close Dialog&#39; + &#39;</md-button>&#39; + &#39;</md-dialog-actions>&#39; + &#39;</md-dialog>&#39;, locals: { items: $scope.items }, controller: DialogController }); function DialogController($scope, $mdDialog, items) { $scope.items = items; $scope.closeDialog = function() { $mdDialog.hide(); } } } } })(angular);</hljs><h3 id=\"javascript-promise-api-syntax-custom-dialog-template\">JavaScript: promise API syntax, custom dialog template</h3><hljs lang=\"js\">(function(angular, undefined){ &quot;use strict&quot;; angular .module(&#39;demoApp&#39;, [&#39;ngMaterial&#39;]) .controller(&#39;EmployeeController&#39;, EmployeeEditor) .controller(&#39;GreetingController&#39;, GreetingController); // Fictitious Employee Editor to show how to use simple and complex dialogs. function EmployeeEditor($scope, $mdDialog) { var alert; $scope.showAlert = showAlert; $scope.closeAlert = closeAlert; $scope.showGreeting = showCustomGreeting; $scope.hasAlert = function() { return !!alert }; $scope.userName = $scope.userName || &#39;Bobby&#39;; // Dialog #1 - Show simple alert dialog and cache // reference to dialog instance function showAlert() { alert = $mdDialog.alert() .title(&#39;Attention, &#39; + $scope.userName) .textContent(&#39;This is an example of how easy dialogs can be!&#39;) .ok(&#39;Close&#39;); $mdDialog .show( alert ) .finally(function() { alert = undefined; }); } // Close the specified dialog instance and resolve with &#39;finished&#39; flag // Normally this is not needed, just use &#39;$mdDialog.hide()&#39; to close // the most recent dialog popup. function closeAlert() { $mdDialog.hide( alert, &quot;finished&quot; ); alert = undefined; } // Dialog #2 - Demonstrate more complex dialogs construction and popup. function showCustomGreeting($event) { $mdDialog.show({ targetEvent: $event, template: &#39;<md-dialog>&#39; + &#39;<md-dialog-content>Hello {{ employee }}!</md-dialog-content>&#39; + &#39;<md-dialog-actions>&#39; + &#39;<md-button ng-click=\"closeDialog()\" class=\"md-primary\">&#39; + &#39; Close Greeting&#39; + &#39;</md-button>&#39; + &#39;</md-dialog-actions>&#39; + &#39;</md-dialog>&#39;, controller: &#39;GreetingController&#39;, onComplete: afterShowAnimation, locals: { employee: $scope.userName } }); // When the &#39;enter&#39; animation finishes... function afterShowAnimation(scope, element, options) { // post-show code here: DOM element focus, etc. } } // Dialog #3 - Demonstrate use of ControllerAs and passing $scope to dialog // Here we used ng-controller=&quot;GreetingController as vm&quot; and // $scope.vm ===<controller instance=\"\">function showCustomGreeting() { $mdDialog.show({ clickOutsideToClose: true, scope: $scope, // use parent scope in template preserveScope: true, // do not forget this if use parent scope // Since GreetingController is instantiated with ControllerAs syntax // AND we are passing the parent &#39;$scope&#39; to the dialog, we MUST // use &#39;vm.<xxx>&#39; in the template markup template: &#39;<md-dialog>&#39; + &#39;<md-dialog-content>&#39; + &#39; Hi There {{vm.employee}}&#39; + &#39;</md-dialog-content>&#39; + &#39;</md-dialog>&#39;, controller: function DialogController($scope, $mdDialog) { $scope.closeDialog = function() { $mdDialog.hide(); } } }); } } // Greeting controller used with the more complex &#39;showCustomGreeting()&#39; custom dialog function GreetingController($scope, $mdDialog, employee) { // Assigned from construction <code>locals</code> options... $scope.employee = employee; $scope.closeDialog = function() { // Easily hides most recent dialog shown... // no specific instance reference is needed. $mdDialog.hide(); }; } })(angular);</xxx></controller></hljs></section><section class=\"api-section\"><h2>Methods</h2><br><ul class=\"methods\"><li id=\"alert\"><h3 class=\"method-function-syntax\"><code class=\"method-function-syntax\">$mdDialog.alert();\r\n</code></h3><div class=\"service-desc\"><p>Builds a preconfigured dialog with the specified message.</p></div><div class=\"method-param-table\"><table class=\"md-api-table\"><thead><tr><th>Returns</th><th>Description</th></tr></thead><tbody><tr><td><code class=\"api-type label type-hint type-hint-obj\">obj</code></td><td class=\"description\"><p>an <code>$mdDialogPreset</code> with the chainable configuration methods:</p><ul><li>$mdDialogPreset#title(string) - Sets the alert title.</li><li>$mdDialogPreset#textContent(string) - Sets the alert message.</li><li>$mdDialogPreset#htmlContent(string) - Sets the alert message as HTML. Requires ngSanitize module to be loaded. HTML is not run through Angular&#39;s compiler.</li><li>$mdDialogPreset#ok(string) - Sets the alert &quot;Okay&quot; button text.</li><li>$mdDialogPreset#theme(string) - Sets the theme of the alert dialog.</li><li>$mdDialogPreset#targetEvent(DOMClickEvent=) - A click&#39;s event object. When passed in as an option, the location of the click will be used as the starting point for the opening animation of the the dialog.</li></ul></td></tr></tbody></table></div></li><li id=\"confirm\"><h3 class=\"method-function-syntax\"><code class=\"method-function-syntax\">$mdDialog.confirm();\r\n</code></h3><div class=\"service-desc\"><p>Builds a preconfigured dialog with the specified message. You can call show and the promise returned will be resolved only if the user clicks the confirm action on the dialog.</p></div><div class=\"method-param-table\"><table class=\"md-api-table\"><thead><tr><th>Returns</th><th>Description</th></tr></thead><tbody><tr><td><code class=\"api-type label type-hint type-hint-obj\">obj</code></td><td class=\"description\"><p>an <code>$mdDialogPreset</code> with the chainable configuration methods:</p><p>Additionally, it supports the following methods:</p><ul><li>$mdDialogPreset#title(string) - Sets the confirm title.</li><li>$mdDialogPreset#textContent(string) - Sets the confirm message.</li><li>$mdDialogPreset#htmlContent(string) - Sets the confirm message as HTML. Requires ngSanitize module to be loaded. HTML is not run through Angular&#39;s compiler.</li><li>$mdDialogPreset#ok(string) - Sets the confirm &quot;Okay&quot; button text.</li><li>$mdDialogPreset#cancel(string) - Sets the confirm &quot;Cancel&quot; button text.</li><li>$mdDialogPreset#theme(string) - Sets the theme of the confirm dialog.</li><li>$mdDialogPreset#targetEvent(DOMClickEvent=) - A click&#39;s event object. When passed in as an option, the location of the click will be used as the starting point for the opening animation of the the dialog.</li></ul></td></tr></tbody></table></div></li><li id=\"show\"><h3 class=\"method-function-syntax\"><code class=\"method-function-syntax\">$mdDialog.show(optionsOrPreset);\r\n</code></h3><div class=\"service-desc\"><p>Show a dialog with the specified options.</p></div><div class=\"method-param-table\"><table class=\"md-api-table\"><thead><tr><th>Parameter</th><th>Type</th><th>Description</th></tr></thead><tbody><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\"><b>* optionsOrPreset</b> <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-object\">object</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-object\">object</code></td><td class=\"description\"><p>Either provide an <code>$mdDialogPreset</code> returned from <code>alert()</code>, and <code>confirm()</code>, or an options object with the following properties:</p><ul><li><code>templateUrl</code> - <code>{string=}</code>: The url of a template that will be used as the content of the dialog.</li><li><code>template</code> - <code>{string=}</code>: HTML template to show in the dialog. This <strong>must</strong> be trusted HTML with respect to Angular&#39;s <a href=\"https://docs.angularjs.org/api/ng/service/$sce\">$sce service</a>. This template should <strong>never</strong> be constructed with any kind of user input or user data.</li><li><code>autoWrap</code> - <code>{boolean=}</code>: Whether or not to automatically wrap the template with a <code>&lt;md-dialog&gt;</code> tag if one is not provided. Defaults to true. Can be disabled if you provide a custom dialog directive.</li><li><code>targetEvent</code> - <code>{DOMClickEvent=}</code>: A click&#39;s event object. When passed in as an option, the location of the click will be used as the starting point for the opening animation of the the dialog.</li><li><code>openFrom</code> - <code>{string|Element|object}</code>: The query selector, DOM element or the Rect object that is used to determine the bounds (top, left, height, width) from which the Dialog will originate.</li><li><code>closeTo</code> - <code>{string|Element|object}</code>: The query selector, DOM element or the Rect object that is used to determine the bounds (top, left, height, width) to which the Dialog will target.</li><li><code>scope</code> - <code>{object=}</code>: the scope to link the template / controller to. If none is specified, it will create a new isolate scope. This scope will be destroyed when the dialog is removed unless <code>preserveScope</code> is set to true.</li><li><code>preserveScope</code> - <code>{boolean=}</code>: whether to preserve the scope when the element is removed. Default is false</li><li><code>disableParentScroll</code> - <code>{boolean=}</code>: Whether to disable scrolling while the dialog is open. Default true.</li><li><code>hasBackdrop</code> - <code>{boolean=}</code>: Whether there should be an opaque backdrop behind the dialog. Default true.</li><li><code>clickOutsideToClose</code> - <code>{boolean=}</code>: Whether the user can click outside the dialog to close it. Default false.</li><li><code>escapeToClose</code> - <code>{boolean=}</code>: Whether the user can press escape to close the dialog. Default true.</li><li><code>focusOnOpen</code> - <code>{boolean=}</code>: An option to override focus behavior on open. Only disable if focusing some other way, as focus management is required for dialogs to be accessible. Defaults to true.</li><li><code>controller</code> - <code>{function|string=}</code>: The controller to associate with the dialog. The controller will be injected with the local <code>$mdDialog</code>, which passes along a scope for the dialog.</li><li><code>locals</code> - <code>{object=}</code>: An object containing key/value pairs. The keys will be used as names of values to inject into the controller. For example, <code>locals: {three: 3}</code> would inject <code>three</code> into the controller, with the value 3. If <code>bindToController</code> is true, they will be copied to the controller instead.</li><li><code>bindToController</code> - <code>bool</code>: bind the locals to the controller, instead of passing them in. These values will not be available until after initialization.</li><li><code>resolve</code> - <code>{object=}</code>: Similar to locals, except it takes promises as values, and the dialog will not open until all of the promises resolve.</li><li><code>controllerAs</code> - <code>{string=}</code>: An alias to assign the controller to on the scope.</li><li><code>parent</code> - <code>{element=}</code>: The element to append the dialog to. Defaults to appending to the root element of the application.</li><li><code>onShowing</code> `{function=} Callback function used to announce the show() action is starting.</li><li><code>onComplete</code> <code>{function=}</code>: Callback function used to announce when the show() action is finished.</li><li><code>onRemoving</code> <code>{function=}</code>: Callback function used to announce the close/hide() action is starting. This allows developers to run custom animations in parallel the close animations.</li><li><code>fullscreen</code> <code>{boolean=}</code>: An option to apply <code>.md-dialog-fullscreen</code> class on open.</li></ul></td></tr></tbody></table><table class=\"md-api-table\"><thead><tr><th>Returns</th><th>Description</th></tr></thead><tbody><tr><td><code class=\"api-type label type-hint type-hint-promise\">promise</code></td><td class=\"description\"><p>A promise that can be resolved with <code>$mdDialog.hide()</code> or rejected with <code>$mdDialog.cancel()</code>.</p></td></tr></tbody></table></div></li><li id=\"hide\"><h3 class=\"method-function-syntax\"><code class=\"method-function-syntax\">$mdDialog.hide([response]);\r\n</code></h3><div class=\"service-desc\"><p>Hide an existing dialog and resolve the promise returned from <code>$mdDialog.show()</code>.</p></div><div class=\"method-param-table\"><table class=\"md-api-table\"><thead><tr><th>Parameter</th><th>Type</th><th>Description</th></tr></thead><tbody><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">response <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-object\">*</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-object\">*</code></td><td class=\"description\"><p>An argument for the resolved promise.</p></td></tr></tbody></table><table class=\"md-api-table\"><thead><tr><th>Returns</th><th>Description</th></tr></thead><tbody><tr><td><code class=\"api-type label type-hint type-hint-promise\">promise</code></td><td class=\"description\"><p>A promise that is resolved when the dialog has been closed.</p></td></tr></tbody></table></div></li><li id=\"cancel\"><h3 class=\"method-function-syntax\"><code class=\"method-function-syntax\">$mdDialog.cancel([response]);\r\n</code></h3><div class=\"service-desc\"><p>Hide an existing dialog and reject the promise returned from <code>$mdDialog.show()</code>.</p></div><div class=\"method-param-table\"><table class=\"md-api-table\"><thead><tr><th>Parameter</th><th>Type</th><th>Description</th></tr></thead><tbody><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">response <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-object\">*</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-object\">*</code></td><td class=\"description\"><p>An argument for the rejected promise.</p></td></tr></tbody></table><table class=\"md-api-table\"><thead><tr><th>Returns</th><th>Description</th></tr></thead><tbody><tr><td><code class=\"api-type label type-hint type-hint-promise\">promise</code></td><td class=\"description\"><p>A promise that is resolved when the dialog has been closed.</p></td></tr></tbody></table></div></li></ul></section></div></div>");
$templateCache.put("app/main/components/material-docs/partials/api/material.components.divider/directive/mdDivider.html","<div class=\"doc-content\"><header class=\"api-profile-header\"><h2 class=\"md-display-1\">{{currentDoc.name}} API Documentation</h2></header><div layout=\"row\" class=\"api-options-bar with-icon\"></div><div class=\"api-profile-description\"><p>Dividers group and separate content within lists and page layouts using strong visual and spatial distinctions. This divider is a thin rule, lightweight enough to not distract the user from content.</p></div><div><section class=\"api-section\"><h2 id=\"Usage\">Usage</h2><hljs lang=\"html\"><md-divider></md-divider><md-divider md-inset=\"\"></md-divider></hljs></section><div class=\"api-param-section\"><h2>Attributes</h2><div class=\"api-param-table\"><table class=\"md-api-table\"><thead><tr><th>Parameter</th><th>Type</th><th>Description</th></tr></thead><tbody><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">md-inset <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-boolean\">boolean</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-boolean\">boolean</code></td><td class=\"description\"><p>Add this attribute to activate the inset divider style.</p></td></tr></tbody></table></div></div></div></div>");
$templateCache.put("app/main/components/material-docs/partials/api/material.components.fabActions/directive/mdFabActions.html","<div class=\"doc-content\"><header class=\"api-profile-header\"><h2 class=\"md-display-1\">{{currentDoc.name}} API Documentation</h2></header><div layout=\"row\" class=\"api-options-bar with-icon\"></div><div class=\"api-profile-description\"><p>The <code>&lt;md-fab-actions&gt;</code> directive is used inside of a <code>&lt;md-fab-speed-dial&gt;</code> or <code>&lt;md-fab-toolbar&gt;</code> directive to mark an element (or elements) as the actions and setup the proper event listeners.</p></div><div><section class=\"api-section\"><h2 id=\"Usage\">Usage</h2><p>See the <code>&lt;md-fab-speed-dial&gt;</code> or <code>&lt;md-fab-toolbar&gt;</code> directives for example usage.</p></section></div></div>");
$templateCache.put("app/main/components/material-docs/partials/api/material.components.fabSpeedDial/directive/mdFabSpeedDial.html","<div class=\"doc-content\"><header class=\"api-profile-header\"><h2 class=\"md-display-1\">{{currentDoc.name}} API Documentation</h2></header><div layout=\"row\" class=\"api-options-bar with-icon\"></div><div class=\"api-profile-description\"><p>The <code>&lt;md-fab-speed-dial&gt;</code> directive is used to present a series of popup elements (usually <code>&lt;md-button&gt;</code>s) for quick access to common actions.</p><p>There are currently two animations available by applying one of the following classes to the component:</p><ul><li><code>md-fling</code> - The speed dial items appear from underneath the trigger and move into their appropriate positions.</li><li><code>md-scale</code> - The speed dial items appear in their proper places by scaling from 0% to 100%.</li></ul><p>You may also easily position the trigger by applying one one of the following classes to the <code>&lt;md-fab-speed-dial&gt;</code> element:</p><ul><li><code>md-fab-top-left</code></li><li><code>md-fab-top-right</code></li><li><code>md-fab-bottom-left</code></li><li><code>md-fab-bottom-right</code></li></ul><p>These CSS classes use <code>position: absolute</code>, so you need to ensure that the container element also uses <code>position: absolute</code> or <code>position: relative</code> in order for them to work.</p><p>Additionally, you may use the standard <code>ng-mouseenter</code> and <code>ng-mouseleave</code> directives to open or close the speed dial. However, if you wish to allow users to hover over the empty space where the actions will appear, you must also add the <code>md-hover-full</code> class to the speed dial element. Without this, the hover effect will only occur on top of the trigger.</p></div><div><section class=\"api-section\"><h2 id=\"Usage\">Usage</h2><hljs lang=\"html\"><md-fab-speed-dial md-direction=\"up\" class=\"md-fling\"><md-fab-trigger><md-button aria-label=\"Add...\"><md-icon icon=\"/assets/angular-material-assets/img/icons/plus.svg\"></md-icon></md-button></md-fab-trigger><md-fab-actions><md-button aria-label=\"Add User\"><md-icon icon=\"/assets/angular-material-assets/img/icons/user.svg\"></md-icon></md-button><md-button aria-label=\"Add Group\"><md-icon icon=\"/assets/angular-material-assets/img/icons/group.svg\"></md-icon></md-button></md-fab-actions></md-fab-speed-dial></hljs></section><div class=\"api-param-section\"><h2>Attributes</h2><div class=\"api-param-table\"><table class=\"md-api-table\"><thead><tr><th>Parameter</th><th>Type</th><th>Description</th></tr></thead><tbody><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\"><b>* md-direction</b> <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-string\">string</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-string\">string</code></td><td class=\"description\"><p>From which direction you would like the speed dial to appear relative to the trigger element.</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">md-open <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-expression\">expression</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-expression\">expression</code></td><td class=\"description\"><p>Programmatically control whether or not the speed-dial is visible.</p></td></tr></tbody></table></div></div></div></div>");
$templateCache.put("app/main/components/material-docs/partials/api/material.components.fabSpeedDial/directive/mdFabTrigger.html","<div class=\"doc-content\"><header class=\"api-profile-header\"><h2 class=\"md-display-1\">{{currentDoc.name}} API Documentation</h2></header><div layout=\"row\" class=\"api-options-bar with-icon\"></div><div class=\"api-profile-description\"><p>The <code>&lt;md-fab-trigger&gt;</code> directive is used inside of a <code>&lt;md-fab-speed-dial&gt;</code> or <code>&lt;md-fab-toolbar&gt;</code> directive to mark an element (or elements) as the trigger and setup the proper event listeners.</p></div><div><section class=\"api-section\"><h2 id=\"Usage\">Usage</h2><p>See the <code>&lt;md-fab-speed-dial&gt;</code> or <code>&lt;md-fab-toolbar&gt;</code> directives for example usage.</p></section></div></div>");
$templateCache.put("app/main/components/material-docs/partials/api/material.components.fabToolbar/directive/mdFabToolbar.html","<div class=\"doc-content\"><header class=\"api-profile-header\"><h2 class=\"md-display-1\">{{currentDoc.name}} API Documentation</h2></header><div layout=\"row\" class=\"api-options-bar with-icon\"></div><div class=\"api-profile-description\"><p>The <code>&lt;md-fab-toolbar&gt;</code> directive is used present a toolbar of elements (usually <code>&lt;md-button&gt;</code>s) for quick access to common actions when a floating action button is activated (via click or keyboard navigation).</p><p>You may also easily position the trigger by applying one one of the following classes to the <code>&lt;md-fab-toolbar&gt;</code> element:</p><ul><li><code>md-fab-top-left</code></li><li><code>md-fab-top-right</code></li><li><code>md-fab-bottom-left</code></li><li><code>md-fab-bottom-right</code></li></ul><p>These CSS classes use <code>position: absolute</code>, so you need to ensure that the container element also uses <code>position: absolute</code> or <code>position: relative</code> in order for them to work.</p></div><div><section class=\"api-section\"><h2 id=\"Usage\">Usage</h2><hljs lang=\"html\"><md-fab-toolbar md-direction=\"left\"><md-fab-trigger><md-button aria-label=\"Add...\"><md-icon icon=\"/assets/angular-material-assets/img/icons/plus.svg\"></md-icon></md-button></md-fab-trigger><md-fab-actions><md-button aria-label=\"Add User\"><md-icon icon=\"/assets/angular-material-assets/img/icons/user.svg\"></md-icon></md-button><md-button aria-label=\"Add Group\"><md-icon icon=\"/assets/angular-material-assets/img/icons/group.svg\"></md-icon></md-button></md-fab-actions></md-fab-toolbar></hljs></section><div class=\"api-param-section\"><h2>Attributes</h2><div class=\"api-param-table\"><table class=\"md-api-table\"><thead><tr><th>Parameter</th><th>Type</th><th>Description</th></tr></thead><tbody><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\"><b>* md-direction</b> <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-string\">string</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-string\">string</code></td><td class=\"description\"><p>From which direction you would like the toolbar items to appear relative to the trigger element. Supports <code>left</code> and <code>right</code> directions.</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">md-open <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-expression\">expression</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-expression\">expression</code></td><td class=\"description\"><p>Programmatically control whether or not the toolbar is visible.</p></td></tr></tbody></table></div></div></div></div>");
$templateCache.put("app/main/components/material-docs/partials/api/material.components.gridList/directive/mdGridList.html","<div class=\"doc-content\"><header class=\"api-profile-header\"><h2 class=\"md-display-1\">{{currentDoc.name}} API Documentation</h2></header><div layout=\"row\" class=\"api-options-bar with-icon\"></div><div class=\"api-profile-description\"><p>Grid lists are an alternative to standard list views. Grid lists are distinct from grids used for layouts and other visual presentations.</p><p>A grid list is best suited to presenting a homogenous data type, typically images, and is optimized for visual comprehension and differentiating between like data types.</p><p>A grid list is a continuous element consisting of tessellated, regular subdivisions called cells that contain tiles (<code>md-grid-tile</code>).</p><p><img src=\"//material-design.storage.googleapis.com/publish/v_2/material_ext_publish/0Bx4BSt6jniD7OVlEaXZ5YmU1Xzg/components_grids_usage2.png\" style=\"width: 300px; height: auto; margin-right: 16px;\" alt=\"Concept of grid explained visually\"> <img src=\"//material-design.storage.googleapis.com/publish/v_2/material_ext_publish/0Bx4BSt6jniD7VGhsOE5idWlJWXM/components_grids_usage3.png\" style=\"width: 300px; height: auto;\" alt=\"Grid concepts legend\"></p><p>Cells are arrayed vertically and horizontally within the grid.</p><p>Tiles hold content and can span one or more cells vertically or horizontally.</p><h3 id=\"responsive-attributes\">Responsive Attributes</h3><p>The <code>md-grid-list</code> directive supports &quot;responsive&quot; attributes, which allow different <code>md-cols</code>, <code>md-gutter</code> and <code>md-row-height</code> values depending on the currently matching media query (as defined in <code>$mdConstant.MEDIA</code>).</p><p>In order to set a responsive attribute, first define the fallback value with the standard attribute name, then add additional attributes with the following convention: <code>{base-attribute-name}-{media-query-name}=&quot;{value}&quot;</code> (ie. <code>md-cols-lg=&quot;8&quot;</code>)</p></div><div><section class=\"api-section\"><h2 id=\"Usage\">Usage</h2><p>Basic:</p><hljs lang=\"html\"><md-grid-list md-cols=\"5\" md-gutter=\"1em\" md-row-height=\"4:3\"><md-grid-tile></md-grid-tile></md-grid-list></hljs><p>Fixed-height rows:</p><hljs lang=\"html\"><md-grid-list md-cols=\"4\" md-row-height=\"200px\" ...=\"\"><md-grid-tile></md-grid-tile></md-grid-list></hljs><p>Fit rows:</p><hljs lang=\"html\"><md-grid-list md-cols=\"4\" md-row-height=\"fit\" style=\"height: 400px;\" ...=\"\"><md-grid-tile></md-grid-tile></md-grid-list></hljs><p>Using responsive attributes:</p><hljs lang=\"html\"><md-grid-list md-cols-sm=\"2\" md-cols-md=\"4\" md-cols-lg=\"8\" md-cols-gt-lg=\"12\" ...=\"\"><md-grid-tile></md-grid-tile></md-grid-list></hljs></section><div class=\"api-param-section\"><h2>Attributes</h2><div class=\"api-param-table\"><table class=\"md-api-table\"><thead><tr><th>Parameter</th><th>Type</th><th>Description</th></tr></thead><tbody><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\"><b>* md-cols</b> <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-number\">number</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-number\">number</code></td><td class=\"description\"><p>Number of columns in the grid.</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\"><b>* md-row-height</b> <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-string\">string</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-string\">string</code></td><td class=\"description\"><p>One of</p><ul><li>CSS length - Fixed height rows (eg. <code>8px</code> or <code>1rem</code>)</li><li><code>{width}:{height}</code> - Ratio of width to height (eg. <code>md-row-height=&quot;16:9&quot;</code>)</li><li><code>&quot;fit&quot;</code> - Height will be determined by subdividing the available height by the number of rows</li></ul></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">md-gutter <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-string\">string</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-string\">string</code></td><td class=\"description\"><p>The amount of space between tiles in CSS units (default 1px)</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">md-on-layout <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-expression\">expression</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-expression\">expression</code></td><td class=\"description\"><p>Expression to evaluate after layout. Event object is available as <code>$event</code>, and contains performance information.</p></td></tr></tbody></table></div></div></div></div>");
$templateCache.put("app/main/components/material-docs/partials/api/material.components.gridList/directive/mdGridTile.html","<div class=\"doc-content\"><header class=\"api-profile-header\"><h2 class=\"md-display-1\">{{currentDoc.name}} API Documentation</h2></header><div layout=\"row\" class=\"api-options-bar with-icon\"></div><div class=\"api-profile-description\"><p>Tiles contain the content of an <code>md-grid-list</code>. They span one or more grid cells vertically or horizontally, and use <code>md-grid-tile-{footer,header}</code> to display secondary content.</p><h3 id=\"responsive-attributes\">Responsive Attributes</h3><p>The <code>md-grid-tile</code> directive supports &quot;responsive&quot; attributes, which allow different <code>md-rowspan</code> and <code>md-colspan</code> values depending on the currently matching media query (as defined in <code>$mdConstant.MEDIA</code>).</p><p>In order to set a responsive attribute, first define the fallback value with the standard attribute name, then add additional attributes with the following convention: <code>{base-attribute-name}-{media-query-name}=&quot;{value}&quot;</code> (ie. <code>md-colspan-sm=&quot;4&quot;</code>)</p></div><div><section class=\"api-section\"><h2 id=\"Usage\">Usage</h2><p>With header:</p><hljs lang=\"html\"><md-grid-tile><md-grid-tile-header><h3>This is a header</h3></md-grid-tile-header></md-grid-tile></hljs><p>With footer:</p><hljs lang=\"html\"><md-grid-tile><md-grid-tile-footer><h3>This is a footer</h3></md-grid-tile-footer></md-grid-tile></hljs><p>Spanning multiple rows/columns:</p><hljs lang=\"html\"><md-grid-tile md-colspan=\"2\" md-rowspan=\"3\"></md-grid-tile></hljs><p>Responsive attributes:</p><hljs lang=\"html\"><md-grid-tile md-colspan=\"1\" md-colspan-sm=\"3\" md-colspan-md=\"5\"></md-grid-tile></hljs></section><div class=\"api-param-section\"><h2>Attributes</h2><div class=\"api-param-table\"><table class=\"md-api-table\"><thead><tr><th>Parameter</th><th>Type</th><th>Description</th></tr></thead><tbody><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">md-colspan <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-number\">number</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-number\">number</code></td><td class=\"description\"><p>The number of columns to span (default 1). Cannot exceed the number of columns in the grid. Supports interpolation.</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">md-rowspan <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-number\">number</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-number\">number</code></td><td class=\"description\"><p>The number of rows to span (default 1). Supports interpolation.</p></td></tr></tbody></table></div></div></div></div>");
$templateCache.put("app/main/components/material-docs/partials/api/material.components.icon/directive/mdIcon.html","<div class=\"doc-content\"><header class=\"api-profile-header\"><h2 class=\"md-display-1\">{{currentDoc.name}} API Documentation</h2></header><div layout=\"row\" class=\"api-options-bar with-icon\"></div><div class=\"api-profile-description\"><p>The <code>md-icon</code> directive makes it easier to use vector-based icons in your app (as opposed to raster-based icons types like PNG). The directive supports both icon fonts and SVG icons.</p><p>Icons should be consider view-only elements that should not be used directly as buttons; instead nest a <code>&lt;md-icon&gt;</code> inside a <code>md-button</code> to add hover and click features.</p><h3 id=\"icon-fonts\">Icon fonts</h3><p>Icon fonts are a technique in which you use a font where the glyphs in the font are your icons instead of text. Benefits include a straightforward way to bundle everything into a single HTTP request, simple scaling, easy color changing, and more.</p><p><code>md-icon</code> lets you consume an icon font by letting you reference specific icons in that font by name rather than character code.</p><h3 id=\"svg\">SVG</h3><p>For SVGs, the problem with using <code>&lt;img&gt;</code> or a CSS <code>background-image</code> is that you can&#39;t take advantage of some SVG features, such as styling specific parts of the icon with CSS or SVG animation.</p><p><code>md-icon</code> makes it easier to use SVG icons by <em>inlining</em> the SVG into an <code>&lt;svg&gt;</code> element in the document. The most straightforward way of referencing an SVG icon is via URL, just like a traditional <code>&lt;img&gt;</code>. <code>$mdIconProvider</code>, as a convenience, lets you <em>name</em> an icon so you can reference it by name instead of URL throughout your templates.</p><p>Additionally, you may not want to make separate HTTP requests for every icon, so you can bundle your SVG icons together and pre-load them with $mdIconProvider as an icon set. An icon set can also be given a name, which acts as a namespace for individual icons, so you can reference them like <code>&quot;social:cake&quot;</code>.</p><p>When using SVGs, both external SVGs (via URLs) or sets of SVGs [from icon sets] can be easily loaded and used.When use font-icons, developers must following three (3) simple steps:</p><ol><li>Load the font library. e.g.<br>&lt;link href=&quot;<a href=\"https://fonts.googleapis.com/icon?family=Material+Icons\">https://fonts.googleapis.com/icon?family=Material+Icons</a>&quot; rel=&quot;stylesheet&quot;&gt;</li><li>Use either (a) font-icon class names or (b) font ligatures to render the font glyph by using its textual name</li><li>Use &lt;md-icon md-font-icon=&quot;classname&quot; /&gt; or<br>use &lt;md-icon md-font-set=&quot;font library classname or alias&quot;&gt; textual_name &lt;/md-icon&gt; or<br>use &lt;md-icon md-font-set=&quot;font library classname or alias&quot;&gt; numerical_character_reference &lt;/md-icon&gt;</li></ol><p>Full details for these steps can be found:</p><ul><li><a href=\"http://google.github.io/material-design-icons/\">http://google.github.io/material-design-icons/</a></li><li><a href=\"http://google.github.io/material-design-icons/#icon-font-for-the-web\">http://google.github.io/material-design-icons/#icon-font-for-the-web</a></li></ul><p>The Material Design icon style <code>.material-icons</code> and the icon font references are published in Material Design Icons:</p><ul><li><a href=\"http://www.google.com/design/icons/\">http://www.google.com/design/icons/</a></li><li><a href=\"https://www.google.com/design/icons/#ic_accessibility\">https://www.google.com/design/icons/#ic_accessibility</a></li></ul><p></p><h2 id=\"material_design_icons\">Material Design Icons</h2>Using the Material Design Icon-Selector, developers can easily and quickly search for a Material Design font-icon and determine its textual name and character reference code. Click on any icon to see the slide-up information panel with details regarding a SVG download or information on the font-icon usage.<p></p><p><a href=\"https://www.google.com/design/icons/#ic_accessibility\" target=\"_blank\" style=\"border-bottom:none;\"><img src=\"https://cloud.githubusercontent.com/assets/210413/7902490/fe8dd14c-0780-11e5-98fb-c821cc6475e6.png\" aria-label=\"Material Design Icon-Selector\" style=\"max-width:75%;padding-left:10%\"></a></p><p><span class=\"image_caption\">Click on the image above to link to the <a href=\"https://www.google.com/design/icons/#ic_accessibility\" target=\"_blank\">Material Design Icon-Selector</a>.</span></p></div><div><section class=\"api-section\"><h2 id=\"Usage\">Usage</h2><p>When using SVGs:</p><hljs lang=\"html\"><md-icon md-svg-icon=\"social:android\" aria-label=\"android\"></md-icon><md-icon md-svg-src=\"/android.svg\" aria-label=\"android\"></md-icon><md-icon md-svg-src=\"{{ getAndroid() }}\" aria-label=\"android\"></md-icon></hljs><p>Use the <code>$mdIconProvider</code> to configure your application with svg iconsets.</p><hljs lang=\"js\">angular.module(&#39;appSvgIconSets&#39;, [&#39;ngMaterial&#39;]) .controller(&#39;DemoCtrl&#39;, function($scope) {}) .config(function($mdIconProvider) { $mdIconProvider .iconSet(&#39;social&#39;, &#39;assets/angular-material-assets/img/icons/sets/social-icons.svg&#39;, 24) .defaultIconSet(&#39;assets/angular-material-assets/img/icons/sets/core-icons.svg&#39;, 24); });</hljs><p>When using Font Icons with classnames:</p><hljs lang=\"html\"><md-icon md-font-icon=\"android\" aria-label=\"android\"></md-icon><md-icon class=\"icon_home\" aria-label=\"Home\"></md-icon></hljs><p>When using Material Font Icons with ligatures:</p><hljs lang=\"html\"><md-icon>face</md-icon><md-icon md-font-set=\"material-icons\">face</md-icon><md-icon>#xE87C;</md-icon><md-icon class=\"material-icons md-light md-48\">face</md-icon></hljs><p>When using other Font-Icon libraries:</p><hljs lang=\"js\">// Specify a font-icon style alias angular.config(function($mdIconProvider) { $mdIconProvider.fontSet(&#39;fa&#39;, &#39;fontawesome&#39;); });</hljs><hljs lang=\"html\"><md-icon md-font-set=\"fa\">email</md-icon></hljs></section><div class=\"api-param-section\"><h2>Attributes</h2><div class=\"api-param-table\"><table class=\"md-api-table\"><thead><tr><th>Parameter</th><th>Type</th><th>Description</th></tr></thead><tbody><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\"><b>* md-font-icon</b> <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-string\">string</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-string\">string</code></td><td class=\"description\"><p>String name of CSS icon associated with the font-face will be used to render the icon. Requires the fonts and the named CSS styles to be preloaded.</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\"><b>* md-font-set</b> <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-string\">string</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-string\">string</code></td><td class=\"description\"><p>CSS style name associated with the font library; which will be assigned as the class for the font-icon ligature. This value may also be an alias that is used to lookup the classname; internally use <code>$mdIconProvider.fontSet(&lt;alias&gt;)</code> to determine the style name.</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\"><b>* md-svg-src</b> <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-string\">string</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-string\">string</code></td><td class=\"description\"><p>String URL (or expression) used to load, cache, and display an external SVG.</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\"><b>* md-svg-icon</b> <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-string\">string</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-string\">string</code></td><td class=\"description\"><p>md-svg-icon String name used for lookup of the icon from the internal cache; interpolated strings or expressions may also be used. Specific set names can be used with the syntax <code>&lt;set name&gt;:&lt;icon name&gt;</code>.<br><br>To use icon sets, developers are required to pre-register the sets using the <code>$mdIconProvider</code> service.</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">aria-label <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-string\">string</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-string\">string</code></td><td class=\"description\"><p>Labels icon for accessibility. If an empty string is provided, icon will be hidden from accessibility layer with <code>aria-hidden=&quot;true&quot;</code>. If there&#39;s no aria-label on the icon nor a label on the parent element, a warning will be logged to the console.</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">alt <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-string\">string</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-string\">string</code></td><td class=\"description\"><p>Labels icon for accessibility. If an empty string is provided, icon will be hidden from accessibility layer with <code>aria-hidden=&quot;true&quot;</code>. If there&#39;s no alt on the icon nor a label on the parent element, a warning will be logged to the console.</p></td></tr></tbody></table></div></div></div></div>");
$templateCache.put("app/main/components/material-docs/partials/api/material.components.icon/service/$mdIcon.html","<div class=\"doc-content\"><header class=\"api-profile-header\"><h2 class=\"md-display-1\">{{currentDoc.name}} API Documentation</h2></header><div layout=\"row\" class=\"api-options-bar with-icon\"></div><div class=\"api-profile-description\"><p>The <code>$mdIcon</code> service is a function used to lookup SVG icons.</p></div><div><section class=\"api-section\"><h2 id=\"Usage\">Usage</h2><hljs lang=\"js\">function SomeDirective($mdIcon) { // See if the icon has already been loaded, if not // then lookup the icon from the registry cache, load and cache // it for future requests. // NOTE: ID queries require configuration with $mdIconProvider $mdIcon(&#39;android&#39;).then(function(iconEl) { element.append(iconEl); }); $mdIcon(&#39;work:chair&#39;).then(function(iconEl) { element.append(iconEl); }); // Load and cache the external SVG using a URL $mdIcon(&#39;assets/angular-material-assets/img/icons/android.svg&#39;).then(function(iconEl) { element.append(iconEl); }); };</hljs><p>NOTE: The <code>&lt;md-icon /&gt;</code> directive internally uses the <code>$mdIcon</code> service to query, loaded, and instantiate SVG DOM elements.</p></section><section class=\"api-section\"><div class=\"api-param-section\"><h2>Arguments</h2><div class=\"api-param-table\"><table class=\"md-api-table\"><thead><tr><th>Parameter</th><th>Type</th><th>Description</th></tr></thead><tbody><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\"><b>* id</b> <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-string\">string</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-string\">string</code></td><td class=\"description\"><p>Query value for a unique Id or URL. If the argument is a URL, then the service will retrieve the icon element from its internal cache or load the icon and cache it first. If the value is not a URL-type string, then an ID lookup is performed. The Id may be a unique icon ID or may include an iconSet ID prefix.</p><p>For the <strong>id</strong> query to work properly, this means that all id-to-URL mappings must have been previously configured using the <code>$mdIconProvider</code>.</p></td></tr></tbody></table></div></div><section class=\"api-section\"></section><div class=\"api-param-table\"><table class=\"md-api-table\"><thead><tr><th>Returns</th><th>Description</th></tr></thead><tbody><tr><td><code class=\"api-type label type-hint type-hint-obj\">obj</code></td><td class=\"description\"><p>Clone of the initial SVG DOM element; which was created from the SVG markup in the SVG data file.</p></td></tr></tbody></table></div></section></div></div>");
$templateCache.put("app/main/components/material-docs/partials/api/material.components.icon/service/$mdIconProvider.html","<div class=\"doc-content\"><header class=\"api-profile-header\"><h2 class=\"md-display-1\">{{currentDoc.name}} API Documentation</h2></header><div layout=\"row\" class=\"api-options-bar with-icon\"></div><div class=\"api-profile-description\"><p><code>$mdIconProvider</code> is used only to register icon IDs with URLs. These configuration features allow icons and icon sets to be pre-registered and associated with source URLs <strong>before</strong> the <code>&lt;md-icon /&gt;</code> directives are compiled.</p><p>If using font-icons, the developer is responsible for loading the fonts.</p><p>If using SVGs, loading of the actual svg files are deferred to on-demand requests and are loaded internally by the <code>$mdIcon</code> service using the <code>$http</code> service. When an SVG is requested by name/ID, the <code>$mdIcon</code> service searches its registry for the associated source URL; that URL is used to on-demand load and parse the SVG dynamically.</p></div><div><section class=\"api-section\"><h2 id=\"Usage\">Usage</h2><hljs lang=\"js\">app.config(function($mdIconProvider) { // Configure URLs for icons specified by [set:]id. $mdIconProvider .defaultFontSet( &#39;fontawesome&#39; ) .defaultIconSet(&#39;my/app/icons.svg&#39;) // Register a default set of SVG icons .iconSet(&#39;social&#39;, &#39;my/app/social.svg&#39;) // Register a named icon set of SVGs .icon(&#39;android&#39;, &#39;my/app/android.svg&#39;) // Register a specific icon (by name) .icon(&#39;work:chair&#39;, &#39;my/app/chair.svg&#39;); // Register icon in a specific set });</hljs><p>SVG icons and icon sets can be easily pre-loaded and cached using either (a) a build process or (b) a runtime <strong>startup</strong> process (shown below):</p><hljs lang=\"js\">app.config(function($mdIconProvider) { // Register a default set of SVG icon definitions $mdIconProvider.defaultIconSet(&#39;my/app/icons.svg&#39;) }) .run(function($http, $templateCache){ // Pre-fetch icons sources by URL and cache in the $templateCache... // subsequent $http calls will look there first. var urls = [ &#39;imy/app/icons.svg&#39;, &#39;assets/angular-material-assets/img/icons/android.svg&#39;]; angular.forEach(urls, function(url) { $http.get(url, {cache: $templateCache}); }); });</hljs><p>NOTE: the loaded SVG data is subsequently cached internally for future requests.</p></section><section class=\"api-section\"><h2>Methods</h2><br><ul class=\"methods\"><li id=\"icon\"><h3 class=\"method-function-syntax\"><code class=\"method-function-syntax\">$mdIconProvider.icon(id, url, [viewBoxSize]);\r\n</code></h3><div class=\"service-desc\"><p>Register a source URL for a specific icon name; the name may include optional &#39;icon set&#39; name prefix. These icons will later be retrieved from the cache using <code>$mdIcon( &lt;icon name&gt; )</code></p></div><div class=\"method-param-table\"><table class=\"md-api-table\"><thead><tr><th>Parameter</th><th>Type</th><th>Description</th></tr></thead><tbody><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\"><b>* id</b> <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-string\">string</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-string\">string</code></td><td class=\"description\"><p>Icon name/id used to register the icon</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\"><b>* url</b> <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-string\">string</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-string\">string</code></td><td class=\"description\"><p>specifies the external location for the data file. Used internally by <code>$http</code> to load the data or as part of the lookup in <code>$templateCache</code> if pre-loading was configured.</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">viewBoxSize <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-number\">number</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-number\">number</code></td><td class=\"description\"><p>Sets the width and height the icon&#39;s viewBox. It is ignored for icons with an existing viewBox. Default size is 24.</p></td></tr></tbody></table><table class=\"md-api-table\"><thead><tr><th>Returns</th><th>Description</th></tr></thead><tbody><tr><td><code class=\"api-type label type-hint type-hint-obj\">obj</code></td><td class=\"description\"><p>an <code>$mdIconProvider</code> reference; used to support method call chains for the API</p></td></tr></tbody></table></div></li><li id=\"iconSet\"><h3 class=\"method-function-syntax\"><code class=\"method-function-syntax\">$mdIconProvider.iconSet(id, url, [viewBoxSize]);\r\n</code></h3><div class=\"service-desc\"><p>Register a source URL for a &#39;named&#39; set of icons; group of SVG definitions where each definition has an icon id. Individual icons can be subsequently retrieved from this cached set using <code>$mdIcon(&lt;icon set name&gt;:&lt;icon name&gt;)</code></p></div><div class=\"method-param-table\"><table class=\"md-api-table\"><thead><tr><th>Parameter</th><th>Type</th><th>Description</th></tr></thead><tbody><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\"><b>* id</b> <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-string\">string</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-string\">string</code></td><td class=\"description\"><p>Icon name/id used to register the iconset</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\"><b>* url</b> <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-string\">string</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-string\">string</code></td><td class=\"description\"><p>specifies the external location for the data file. Used internally by <code>$http</code> to load the data or as part of the lookup in <code>$templateCache</code> if pre-loading was configured.</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">viewBoxSize <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-number\">number</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-number\">number</code></td><td class=\"description\"><p>Sets the width and height of the viewBox of all icons in the set. It is ignored for icons with an existing viewBox. All icons in the icon set should be the same size. Default value is 24.</p></td></tr></tbody></table><table class=\"md-api-table\"><thead><tr><th>Returns</th><th>Description</th></tr></thead><tbody><tr><td><code class=\"api-type label type-hint type-hint-obj\">obj</code></td><td class=\"description\"><p>an <code>$mdIconProvider</code> reference; used to support method call chains for the API</p></td></tr></tbody></table></div></li><li id=\"defaultIconSet\"><h3 class=\"method-function-syntax\"><code class=\"method-function-syntax\">$mdIconProvider.defaultIconSet(url, [viewBoxSize]);\r\n</code></h3><div class=\"service-desc\"><p>Register a source URL for the default &#39;named&#39; set of icons. Unless explicitly registered, subsequent lookups of icons will failover to search this &#39;default&#39; icon set. Icon can be retrieved from this cached, default set using <code>$mdIcon(&lt;name&gt;)</code></p></div><div class=\"method-param-table\"><table class=\"md-api-table\"><thead><tr><th>Parameter</th><th>Type</th><th>Description</th></tr></thead><tbody><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\"><b>* url</b> <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-string\">string</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-string\">string</code></td><td class=\"description\"><p>specifies the external location for the data file. Used internally by <code>$http</code> to load the data or as part of the lookup in <code>$templateCache</code> if pre-loading was configured.</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">viewBoxSize <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-number\">number</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-number\">number</code></td><td class=\"description\"><p>Sets the width and height of the viewBox of all icons in the set. It is ignored for icons with an existing viewBox. All icons in the icon set should be the same size. Default value is 24.</p></td></tr></tbody></table><table class=\"md-api-table\"><thead><tr><th>Returns</th><th>Description</th></tr></thead><tbody><tr><td><code class=\"api-type label type-hint type-hint-obj\">obj</code></td><td class=\"description\"><p>an <code>$mdIconProvider</code> reference; used to support method call chains for the API</p></td></tr></tbody></table></div></li><li id=\"defaultFontSet\"><h3 class=\"method-function-syntax\"><code class=\"method-function-syntax\">$mdIconProvider.defaultFontSet(name);\r\n</code></h3><div class=\"service-desc\"><p>When using Font-Icons, Angular Material assumes the the Material Design icons will be used and automatically configures the default font-set == &#39;material-icons&#39;. Note that the font-set references the font-icon library class style that should be applied to the <code>&lt;md-icon&gt;</code>.</p><p>Configuring the default means that the attributes <code>md-font-set=&quot;material-icons&quot;</code> or <code>class=&quot;material-icons&quot;</code> do not need to be explicitly declared on the <code>&lt;md-icon&gt;</code> markup. For example:</p><p><code>&lt;md-icon&gt; face &lt;/md-icon&gt;</code> will render as <code>&lt;span class=&quot;material-icons&quot;&gt; face &lt;/span&gt;</code>, and</p><p><code>&lt;md-icon md-font-set=&quot;fa&quot;&gt; face &lt;/md-icon&gt;</code> will render as <code>&lt;span class=&quot;fa&quot;&gt; face &lt;/span&gt;</code></p></div><div class=\"method-param-table\"><table class=\"md-api-table\"><thead><tr><th>Parameter</th><th>Type</th><th>Description</th></tr></thead><tbody><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\"><b>* name</b> <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-string\">string</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-string\">string</code></td><td class=\"description\"><p>of the font-library style that should be applied to the md-icon DOM element</p></td></tr></tbody></table></div></li><li id=\"defaultViewBoxSize\"><h3 class=\"method-function-syntax\"><code class=\"method-function-syntax\">$mdIconProvider.defaultViewBoxSize([viewBoxSize]);\r\n</code></h3><div class=\"service-desc\"><p>While <code>&lt;md-icon /&gt;</code> markup can also be style with sizing CSS, this method configures the default width <strong>and</strong> height used for all icons; unless overridden by specific CSS. The default sizing is (24px, 24px).</p></div><div class=\"method-param-table\"><table class=\"md-api-table\"><thead><tr><th>Parameter</th><th>Type</th><th>Description</th></tr></thead><tbody><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">viewBoxSize <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-number\">number</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-number\">number</code></td><td class=\"description\"><p>Sets the width and height of the viewBox for an icon or an icon set. All icons in a set should be the same size. The default value is 24.</p></td></tr></tbody></table><table class=\"md-api-table\"><thead><tr><th>Returns</th><th>Description</th></tr></thead><tbody><tr><td><code class=\"api-type label type-hint type-hint-obj\">obj</code></td><td class=\"description\"><p>an <code>$mdIconProvider</code> reference; used to support method call chains for the API</p></td></tr></tbody></table></div></li></ul></section></div></div>");
$templateCache.put("app/main/components/material-docs/partials/api/material.components.input/directive/mdInput.html","<div class=\"doc-content\"><header class=\"api-profile-header\"><h2 class=\"md-display-1\">{{currentDoc.name}} API Documentation</h2></header><div layout=\"row\" class=\"api-options-bar with-icon\"></div><div class=\"api-profile-description\"><p>You can use any <code>&lt;input&gt;</code> or <code>&lt;textarea&gt;</code> element as a child of an <code>&lt;md-input-container&gt;</code>. This allows you to build complex forms for data entry.</p></div><div><section class=\"api-section\"><h2 id=\"Usage\">Usage</h2><hljs lang=\"html\"><md-input-container><label>Color</label> <input type=\"text\" ng-model=\"color\" required=\"\" md-maxlength=\"10\"></md-input-container></hljs><h3>With Errors</h3><p><code>md-input-container</code> also supports errors using the standard <code>ng-messages</code> directives and animates the messages when they become visible using from the <code>ngEnter</code>/<code>ngLeave</code> events or the <code>ngShow</code>/<code>ngHide</code> events.</p><p>By default, the messages will be hidden until the input is in an error state. This is based off of the <code>md-is-error</code> expression of the <code>md-input-container</code>. This gives the user a chance to fill out the form before the errors become visible.</p><hljs lang=\"html\"><form name=\"colorForm\"><md-input-container><label>Favorite Color</label> <input name=\"favoriteColor\" ng-model=\"favoriteColor\" required=\"\"><div ng-messages=\"userForm.lastName.$error\"><div ng-message=\"required\">This is required!</div></div></md-input-container></form></hljs><p>We automatically disable this auto-hiding functionality if you provide any of the following visibility directives on the <code>ng-messages</code> container:</p><ul><li><code>ng-if</code></li><li><code>ng-show</code>/<code>ng-hide</code></li><li><code>ng-switch-when</code>/<code>ng-switch-default</code></li></ul><p>You can also disable this functionality manually by adding the <code>md-auto-hide=&quot;false&quot;</code> expression to the <code>ng-messages</code> container. This may be helpful if you always want to see the error messages or if you are building your own visibilty directive.</p><p><em><b>Note:</b> The <code>md-auto-hide</code> attribute is a static string that is only checked upon initialization of the <code>ng-messages</code> directive to see if it equals the string <code>false</code>.</em></p><hljs lang=\"html\"><form name=\"userForm\"><md-input-container><label>Last Name</label> <input name=\"lastName\" ng-model=\"lastName\" required=\"\" md-maxlength=\"10\" minlength=\"4\"><div ng-messages=\"userForm.lastName.$error\" ng-show=\"userForm.lastName.$dirty\"><div ng-message=\"required\">This is required!</div><div ng-message=\"md-maxlength\">That&#39;s too long!</div><div ng-message=\"minlength\">That&#39;s too short!</div></div></md-input-container><md-input-container><label>Biography</label> <textarea name=\"bio\" ng-model=\"biography\" required=\"\" md-maxlength=\"150\"></textarea><div ng-messages=\"userForm.bio.$error\" ng-show=\"userForm.bio.$dirty\"><div ng-message=\"required\">This is required!</div><div ng-message=\"md-maxlength\">That&#39;s too long!</div></div></md-input-container><md-input-container><input aria-label=\"title\" ng-model=\"title\"></md-input-container><md-input-container><input placeholder=\"title\" ng-model=\"title\"></md-input-container></form></hljs><h3>Notes</h3><ul><li>Requires <a href=\"https://docs.angularjs.org/api/ngMessages\">ngMessages</a>.</li><li>Behaves like the <a href=\"https://docs.angularjs.org/api/ng/directive/input\">AngularJS input directive</a>.</li></ul><p>The <code>md-input</code> and <code>md-input-container</code> directives use very specific positioning to achieve the error animation effects. Therefore, it is <em>not</em> advised to use the Layout system inside of the <code>&lt;md-input-container&gt;</code> tags. Instead, use relative or absolute positioning.</p></section><div class=\"api-param-section\"><h2>Attributes</h2><div class=\"api-param-table\"><table class=\"md-api-table\"><thead><tr><th>Parameter</th><th>Type</th><th>Description</th></tr></thead><tbody><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">md-maxlength <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-number\">number</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-number\">number</code></td><td class=\"description\"><p>The maximum number of characters allowed in this input. If this is specified, a character counter will be shown underneath the input.<br><br>The purpose of <strong><code>md-maxlength</code></strong> is exactly to show the max length counter text. If you don&#39;t want the counter text and only need &quot;plain&quot; validation, you can use the &quot;simple&quot; <code>ng-maxlength</code> or maxlength attributes.</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">aria-label <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-string\">string</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-string\">string</code></td><td class=\"description\"><p>Aria-label is required when no label is present. A warning message will be logged in the console if not present.</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">placeholder <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-string\">string</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-string\">string</code></td><td class=\"description\"><p>An alternative approach to using aria-label when the label is not PRESENT. The placeholder text is copied to the aria-label attribute.</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">md-no-autogrow <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-boolean\">boolean</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-boolean\">boolean</code></td><td class=\"description\"><p>When present, textareas will not grow automatically.</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">md-detect-hidden <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-boolean\">boolean</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-boolean\">boolean</code></td><td class=\"description\"><p>When present, textareas will be sized properly when they are revealed after being hidden. This is off by default for performance reasons because it guarantees a reflow every digest cycle.</p></td></tr></tbody></table></div></div></div></div>");
$templateCache.put("app/main/components/material-docs/partials/api/material.components.input/directive/mdInputContainer.html","<div class=\"doc-content\"><header class=\"api-profile-header\"><h2 class=\"md-display-1\">{{currentDoc.name}} API Documentation</h2></header><div layout=\"row\" class=\"api-options-bar with-icon\"></div><div class=\"api-profile-description\"><p><code>&lt;md-input-container&gt;</code> is the parent of any input or textarea element.</p><p>Input and textarea elements will not behave properly unless the md-input-container parent is provided.</p></div><div><section class=\"api-section\"><h2 id=\"Usage\">Usage</h2><hljs lang=\"html\"><md-input-container><label>Username</label> <input type=\"text\" ng-model=\"user.name\"></md-input-container><md-input-container><label>Description</label> <textarea ng-model=\"user.description\"></textarea></md-input-container></hljs></section><div class=\"api-param-section\"><h2>Attributes</h2><div class=\"api-param-table\"><table class=\"md-api-table\"><thead><tr><th>Parameter</th><th>Type</th><th>Description</th></tr></thead><tbody><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">md-is-error <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-expression\">expression</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-expression\">expression</code></td><td class=\"description\"><p>When the given expression evaluates to true, the input container will go into error state. Defaults to erroring if the input has been touched and is invalid.</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">md-no-float <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-boolean\">boolean</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-boolean\">boolean</code></td><td class=\"description\"><p>When present, placeholders will not be converted to floating labels.</p></td></tr></tbody></table></div></div></div></div>");
$templateCache.put("app/main/components/material-docs/partials/api/material.components.list/directive/mdList.html","<div class=\"doc-content\"><header class=\"api-profile-header\"><h2 class=\"md-display-1\">{{currentDoc.name}} API Documentation</h2></header><div layout=\"row\" class=\"api-options-bar with-icon\"></div><div class=\"api-profile-description\"><p>The <code>&lt;md-list&gt;</code> directive is a list container for 1..n <code>&lt;md-list-item&gt;</code> tags.</p></div><div><section class=\"api-section\"><h2 id=\"Usage\">Usage</h2><hljs lang=\"html\"><md-list><md-list-item class=\"md-2-line\" ng-repeat=\"item in todos\"><md-checkbox ng-model=\"item.done\"></md-checkbox><div class=\"md-list-item-text\"><h3>{{item.title}}</h3><p>{{item.description}}</p></div></md-list-item></md-list></hljs></section></div></div>");
$templateCache.put("app/main/components/material-docs/partials/api/material.components.list/directive/mdListItem.html","<div class=\"doc-content\"><header class=\"api-profile-header\"><h2 class=\"md-display-1\">{{currentDoc.name}} API Documentation</h2></header><div layout=\"row\" class=\"api-options-bar with-icon\"></div><div class=\"api-profile-description\"><p>The <code>&lt;md-list-item&gt;</code> directive is a container intended for row items in a <code>&lt;md-list&gt;</code> container. The <code>md-2-line</code> and <code>md-3-line</code> classes can be added to a <code>&lt;md-list-item&gt;</code> to increase the height with 22px and 40px respectively.</p><h2 id=\"css\">CSS</h2><p><code>.md-avatar</code> - class for image avatars</p><p><code>.md-avatar-icon</code> - class for icon avatars</p><p><code>.md-offset</code> - on content without an avatar</p></div><div><section class=\"api-section\"><h2 id=\"Usage\">Usage</h2><hljs lang=\"html\"><md-list><md-list-item><img class=\"md-avatar\" ng-src=\"path/to/img\"> <span>Item content in list</span></md-list-item><md-list-item><md-icon class=\"md-avatar-icon\" md-svg-icon=\"communication:phone\"></md-icon><span>Item content in list</span></md-list-item></md-list></hljs><p><em><strong>Note:</strong> We automatically apply special styling when the inner contents are wrapped inside of a <code>&lt;md-button&gt;</code> tag. This styling is automatically ignored for <code>class=&quot;md-secondary&quot;</code> buttons and you can include a class of <code>class=&quot;md-exclude&quot;</code> if you need to use a non-secondary button that is inside the list, but does not wrap the contents.</em></p></section></div></div>");
$templateCache.put("app/main/components/material-docs/partials/api/material.components.menu/directive/mdMenu.html","<div class=\"doc-content\"><header class=\"api-profile-header\"><h2 class=\"md-display-1\">{{currentDoc.name}} API Documentation</h2></header><div layout=\"row\" class=\"api-options-bar with-icon\"></div><div class=\"api-profile-description\"><p>Menus are elements that open when clicked. They are useful for displaying additional options within the context of an action.</p><p>Every <code>md-menu</code> must specify exactly two child elements. The first element is what is left in the DOM and is used to open the menu. This element is called the trigger element. The trigger element&#39;s scope has access to <code>$mdOpenMenu($event)</code> which it may call to open the menu. By passing $event as argument, the corresponding event is stopped from propagating up the DOM-tree.</p><p>The second element is the <code>md-menu-content</code> element which represents the contents of the menu when it is open. Typically this will contain <code>md-menu-item</code>s, but you can do custom content as well.</p><hljs lang=\"html\"><md-menu><md-button ng-click=\"$mdOpenMenu($event)\" class=\"md-icon-button\" aria-label=\"Open sample menu\"><md-icon md-svg-icon=\"call:phone\"></md-icon></md-button><md-menu-content><md-menu-item><md-button ng-click=\"doSomething()\">Do Something</md-button></md-menu-item></md-menu-content></md-menu></hljs><h2 id=\"sizing-menus\">Sizing Menus</h2><p>The width of the menu when it is open may be specified by specifying a <code>width</code> attribute on the <code>md-menu-content</code> element. See the <a href=\"http://www.google.com/design/spec/components/menus.html#menus-specs\">Material Design Spec</a> for more information.</p><h2 id=\"aligning-menus\">Aligning Menus</h2><p>When a menu opens, it is important that the content aligns with the trigger element. Failure to align menus can result in jarring experiences for users as content suddenly shifts. To help with this, <code>md-menu</code> provides serveral APIs to help with alignment.</p><h3 id=\"target-mode\">Target Mode</h3><p>By default, <code>md-menu</code> will attempt to align the <code>md-menu-content</code> by aligning designated child elements in both the trigger and the menu content.</p><p>To specify the alignment element in the <code>trigger</code> you can use the <code>md-menu-origin</code> attribute on a child element. If no <code>md-menu-origin</code> is specified, the <code>md-menu</code> will be used as the origin element.</p><p>Similarly, the <code>md-menu-content</code> may specify a <code>md-menu-align-target</code> for a <code>md-menu-item</code> to specify the node that it should try and align with.</p><p>In this example code, we specify an icon to be our origin element, and an icon in our menu content to be our alignment target. This ensures that both icons are aligned when the menu opens.</p><hljs lang=\"html\"><md-menu><md-button ng-click=\"$mdOpenMenu($event)\" class=\"md-icon-button\" aria-label=\"Open some menu\"><md-icon md-menu-origin=\"\" md-svg-icon=\"call:phone\"></md-icon></md-button><md-menu-content><md-menu-item><md-button ng-click=\"doSomething()\" aria-label=\"Do something\"><md-icon md-menu-align-target=\"\" md-svg-icon=\"call:phone\"></md-icon>Do Something</md-button></md-menu-item></md-menu-content></md-menu></hljs><p>Sometimes we want to specify alignment on the right side of an element, for example if we have a menu on the right side a toolbar, we want to right align our menu content.</p><p>We can specify the origin by using the <code>md-position-mode</code> attribute on both the <code>x</code> and <code>y</code> axis. Right now only the <code>x-axis</code> has more than one option. You may specify the default mode of <code>target target</code> or <code>target-right target</code> to specify a right-oriented alignment target. See the position section of the demos for more examples.</p><h3 id=\"menu-offsets\">Menu Offsets</h3><p>It is sometimes unavoidable to need to have a deeper level of control for the positioning of a menu to ensure perfect alignment. <code>md-menu</code> provides the <code>md-offset</code> attribute to allow pixel level specificty of adjusting the exact positioning.</p><p>This offset is provided in the format of <code>x y</code> or <code>n</code> where <code>n</code> will be used in both the <code>x</code> and <code>y</code> axis.</p><p>For example, to move a menu by <code>2px</code> from the top, we can use:</p><hljs lang=\"html\"><md-menu md-offset=\"2 0\"></md-menu></hljs><h3 id=\"preventing-close\">Preventing close</h3><p>Sometimes you would like to be able to click on a menu item without having the menu close. To do this, ngMaterial exposes the <code>md-prevent-menu-close</code> attribute which can be added to a button inside a menu to stop the menu from automatically closing. You can then close the menu programatically by injecting <code>$mdMenu</code> and calling <code>$mdMenu.hide()</code>.</p><hljs lang=\"html\"><md-menu-item><md-button ng-click=\"doSomething()\" aria-label=\"Do something\" md-prevent-menu-close=\"md-prevent-menu-close\"><md-icon md-menu-align-target=\"\" md-svg-icon=\"call:phone\"></md-icon>Do Something</md-button></md-menu-item></hljs></div><div><section class=\"api-section\"><h2 id=\"Usage\">Usage</h2><hljs lang=\"html\"><md-menu><md-button ng-click=\"$mdOpenMenu($event)\" class=\"md-icon-button\"><md-icon md-svg-icon=\"call:phone\"></md-icon></md-button><md-menu-content><md-menu-item><md-button ng-click=\"doSomething()\">Do Something</md-button></md-menu-item></md-menu-content></md-menu></hljs></section><div class=\"api-param-section\"><h2>Attributes</h2><div class=\"api-param-table\"><table class=\"md-api-table\"><thead><tr><th>Parameter</th><th>Type</th><th>Description</th></tr></thead><tbody><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\"><b>* md-position-mode</b> <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-string\">string</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-string\">string</code></td><td class=\"description\"><p>The position mode in the form of <code>x</code>, <code>y</code>. Default value is <code>target</code>,<code>target</code>. Right now the <code>x</code> axis also suppports <code>target-right</code>.</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\"><b>* md-offset</b> <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-string\">string</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-string\">string</code></td><td class=\"description\"><p>An offset to apply to the dropdown after positioning <code>x</code>, <code>y</code>. Default value is <code>0</code>,<code>0</code>.</p></td></tr></tbody></table></div></div></div></div>");
$templateCache.put("app/main/components/material-docs/partials/api/material.components.menu-bar/directive/mdMenuBar.html","<div class=\"doc-content\"><header class=\"api-profile-header\"><h2 class=\"md-display-1\">{{currentDoc.name}} API Documentation</h2></header><div layout=\"row\" class=\"api-options-bar with-icon\"></div><div class=\"api-profile-description\"><p>Menu bars are containers that hold multiple menus. They change the behavior and appearence of the <code>md-menu</code> directive to behave similar to an operating system provided menu.</p></div><div><section class=\"api-section\"><h2 id=\"Usage\">Usage</h2><hljs lang=\"html\"><md-menu-bar><md-menu><button ng-click=\"$mdOpenMenu()\">File</button><md-menu-content><md-menu-item><md-button ng-click=\"ctrl.sampleAction(\'share\', $event)\">Share...</md-button></md-menu-item><md-menu-divider></md-menu-divider><md-menu-item><md-menu-item><md-menu><md-button ng-click=\"$mdOpenMenu()\">New</md-button><md-menu-content><md-menu-item><md-button ng-click=\"ctrl.sampleAction(\'New Document\', $event)\">Document</md-button></md-menu-item><md-menu-item><md-button ng-click=\"ctrl.sampleAction(\'New Spreadsheet\', $event)\">Spreadsheet</md-button></md-menu-item><md-menu-item><md-button ng-click=\"ctrl.sampleAction(\'New Presentation\', $event)\">Presentation</md-button></md-menu-item><md-menu-item><md-button ng-click=\"ctrl.sampleAction(\'New Form\', $event)\">Form</md-button></md-menu-item><md-menu-item><md-button ng-click=\"ctrl.sampleAction(\'New Drawing\', $event)\">Drawing</md-button></md-menu-item></md-menu-content></md-menu></md-menu-item></md-menu-item></md-menu-content></md-menu></md-menu-bar></hljs><h2 id=\"menu-bar-controls\">Menu Bar Controls</h2><p>You may place <code>md-menu-items</code> that function as controls within menu bars. There are two modes that are exposed via the <code>type</code> attribute of the <code>md-menu-item</code>. <code>type=&quot;checkbox&quot;</code> will function as a boolean control for the <code>ng-model</code> attribute of the <code>md-menu-item</code>. <code>type=&quot;radio&quot;</code> will function like a radio button, setting the <code>ngModel</code> to the <code>string</code> value of the <code>value</code> attribute. If you need non-string values, you can use <code>ng-value</code> to provide an expression (this is similar to how angular&#39;s native <code>input[type=radio]</code> works.</p><hljs lang=\"html\"><md-menu-bar><md-menu><button ng-click=\"$mdOpenMenu()\">Sample Menu</button><md-menu-content><md-menu-item type=\"checkbox\" ng-model=\"settings.allowChanges\">Allow changes</md-menu-item><md-menu-divider></md-menu-divider><md-menu-item type=\"radio\" ng-model=\"settings.mode\" ng-value=\"1\">Mode 1</md-menu-item><md-menu-item type=\"radio\" ng-model=\"settings.mode\" ng-value=\"1\">Mode 2</md-menu-item><md-menu-item type=\"radio\" ng-model=\"settings.mode\" ng-value=\"1\">Mode 3</md-menu-item></md-menu-content></md-menu></md-menu-bar></hljs><h3 id=\"nesting-menus\">Nesting Menus</h3><p>Menus may be nested within menu bars. This is commonly called cascading menus. To nest a menu place the nested menu inside the content of the <code>md-menu-item</code>.</p><hljs lang=\"html\"><md-menu-item><md-menu><button ng-click=\"$mdOpenMenu()\">New<md-menu-content><md-menu-item><md-button ng-click=\"ctrl.sampleAction(\'New Document\', $event)\">Document</md-button></md-menu-item><md-menu-item><md-button ng-click=\"ctrl.sampleAction(\'New Spreadsheet\', $event)\">Spreadsheet</md-button></md-menu-item><md-menu-item><md-button ng-click=\"ctrl.sampleAction(\'New Presentation\', $event)\">Presentation</md-button></md-menu-item><md-menu-item><md-button ng-click=\"ctrl.sampleAction(\'New Form\', $event)\">Form</md-button></md-menu-item><md-menu-item><md-button ng-click=\"ctrl.sampleAction(\'New Drawing\', $event)\">Drawing</md-button></md-menu-item></md-menu-content></button></md-menu></md-menu-item></hljs></section></div></div>");
$templateCache.put("app/main/components/material-docs/partials/api/material.components.progressCircular/directive/mdProgressCircular.html","<div class=\"doc-content\"><header class=\"api-profile-header\"><h2 class=\"md-display-1\">{{currentDoc.name}} API Documentation</h2></header><div layout=\"row\" class=\"api-options-bar with-icon\"></div><div class=\"api-profile-description\"><p>The circular progress directive is used to make loading content in your app as delightful and painless as possible by minimizing the amount of visual change a user sees before they can view and interact with content.</p><p>For operations where the percentage of the operation completed can be determined, use a determinate indicator. They give users a quick sense of how long an operation will take.</p><p>For operations where the user is asked to wait a moment while something finishes up, and its not necessary to expose what&#39;s happening behind the scenes and how long it will take, use an indeterminate indicator.</p></div><div><section class=\"api-section\"><h2 id=\"Usage\">Usage</h2><hljs lang=\"html\"><md-progress-circular md-mode=\"determinate\" value=\"...\"></md-progress-circular><md-progress-circular md-mode=\"determinate\" ng-value=\"...\"></md-progress-circular><md-progress-circular md-mode=\"determinate\" value=\"...\" md-diameter=\"100\"></md-progress-circular><md-progress-circular md-mode=\"indeterminate\"></md-progress-circular></hljs></section><div class=\"api-param-section\"><h2>Attributes</h2><div class=\"api-param-table\"><table class=\"md-api-table\"><thead><tr><th>Parameter</th><th>Type</th><th>Description</th></tr></thead><tbody><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\"><b>* md-mode</b> <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-string\">string</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-string\">string</code></td><td class=\"description\"><p>Select from one of two modes: <strong>&#39;determinate&#39;</strong> and <strong>&#39;indeterminate&#39;</strong>.</p><p>Note: if the <code>md-mode</code> value is set as undefined or specified as not 1 of the two (2) valid modes, then <code>.ng-hide</code> will be auto-applied as a style to the component.</p><p>Note: if not configured, the <code>md-mode=&quot;indeterminate&quot;</code> will be auto injected as an attribute. If <code>value=&quot;&quot;</code> is also specified, however, then <code>md-mode=&quot;determinate&quot;</code> would be auto-injected instead.</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">value <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-number\">number</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-number\">number</code></td><td class=\"description\"><p>In determinate mode, this number represents the percentage of the circular progress. Default: 0</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">md-diameter <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-number\">number</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-number\">number</code></td><td class=\"description\"><p>This specifies the diamter of the circular progress. The value may be a percentage (eg &#39;25%&#39;) or a pixel-size value (eg &#39;48&#39;). If this attribute is not present then a default value of &#39;48px&#39; is assumed.</p></td></tr></tbody></table></div></div></div></div>");
$templateCache.put("app/main/components/material-docs/partials/api/material.components.progressLinear/directive/mdProgressLinear.html","<div class=\"doc-content\"><header class=\"api-profile-header\"><h2 class=\"md-display-1\">{{currentDoc.name}} API Documentation</h2></header><div layout=\"row\" class=\"api-options-bar with-icon\"></div><div class=\"api-profile-description\"><p>The linear progress directive is used to make loading content in your app as delightful and painless as possible by minimizing the amount of visual change a user sees before they can view and interact with content.</p><p>Each operation should only be represented by one activity indicator For example: one refresh operation should not display both a refresh bar and an activity circle.</p><p>For operations where the percentage of the operation completed can be determined, use a determinate indicator. They give users a quick sense of how long an operation will take.</p><p>For operations where the user is asked to wait a moment while something finishes up, and its not necessary to expose what&#39;s happening behind the scenes and how long it will take, use an indeterminate indicator.</p></div><div><section class=\"api-section\"><h2 id=\"Usage\">Usage</h2><hljs lang=\"html\"><md-progress-linear md-mode=\"determinate\" value=\"...\"></md-progress-linear><md-progress-linear md-mode=\"determinate\" ng-value=\"...\"></md-progress-linear><md-progress-linear md-mode=\"indeterminate\"></md-progress-linear><md-progress-linear md-mode=\"buffer\" value=\"...\" md-buffer-value=\"...\"></md-progress-linear><md-progress-linear md-mode=\"query\"></md-progress-linear></hljs></section><div class=\"api-param-section\"><h2>Attributes</h2><div class=\"api-param-table\"><table class=\"md-api-table\"><thead><tr><th>Parameter</th><th>Type</th><th>Description</th></tr></thead><tbody><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\"><b>* md-mode</b> <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-string\">string</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-string\">string</code></td><td class=\"description\"><p>Select from one of four modes: determinate, indeterminate, buffer or query.</p><p>Note: if the <code>md-mode</code> value is set as undefined or specified as 1 of the four (4) valid modes, then <code>.ng-hide</code> will be auto-applied as a style to the component.</p><p>Note: if not configured, the <code>md-mode=&quot;indeterminate&quot;</code> will be auto injected as an attribute. If <code>value=&quot;&quot;</code> is also specified, however, then <code>md-mode=&quot;determinate&quot;</code> would be auto-injected instead.</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">value <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-number\">number</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-number\">number</code></td><td class=\"description\"><p>In determinate and buffer modes, this number represents the percentage of the primary progress bar. Default: 0</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">md-buffer-value <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-number\">number</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-number\">number</code></td><td class=\"description\"><p>In the buffer mode, this number represents the percentage of the secondary progress bar. Default: 0</p></td></tr></tbody></table></div></div></div></div>");
$templateCache.put("app/main/components/material-docs/partials/api/material.components.radioButton/directive/mdRadioButton.html","<div class=\"doc-content\"><header class=\"api-profile-header\"><h2 class=\"md-display-1\">{{currentDoc.name}} API Documentation</h2></header><div layout=\"row\" class=\"api-options-bar with-icon\"></div><div class=\"api-profile-description\"><p>The <code>&lt;md-radio-button&gt;</code>directive is the child directive required to be used within <code>&lt;md-radio-group&gt;</code> elements.</p><p>While similar to the <code>&lt;input type=&quot;radio&quot; ng-model=&quot;&quot; value=&quot;&quot;&gt;</code> directive, the <code>&lt;md-radio-button&gt;</code> directive provides ink effects, ARIA support, and supports use within named radio groups.</p></div><div><section class=\"api-section\"><h2 id=\"Usage\">Usage</h2><hljs lang=\"html\"><md-radio-button value=\"1\" aria-label=\"Label 1\">Label 1</md-radio-button><md-radio-button ng-model=\"color\" ng-value=\"specialValue\" aria-label=\"Green\">Green</md-radio-button></hljs></section><div class=\"api-param-section\"><h2>Attributes</h2><div class=\"api-param-table\"><table class=\"md-api-table\"><thead><tr><th>Parameter</th><th>Type</th><th>Description</th></tr></thead><tbody><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\"><b>* ngModel</b> <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-string\">string</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-string\">string</code></td><td class=\"description\"><p>Assignable angular expression to data-bind to.</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\"><b>* ngValue</b> <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-string\">string</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-string\">string</code></td><td class=\"description\"><p>Angular expression which sets the value to which the expression should be set when selected.</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\"><b>* value</b> <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-string\">string</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-string\">string</code></td><td class=\"description\"><p>The value to which the expression should be set when selected.</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">ngChange <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-string\">string</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-string\">string</code></td><td class=\"description\"><p>Angular expression to be executed when input changes due to user interaction with the input element.</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">name <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-string\">string</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-string\">string</code></td><td class=\"description\"><p>Property name of the form under which the control is published.</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">aria-label <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-string\">string</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-string\">string</code></td><td class=\"description\"><p>Adds label to radio button for accessibility. Defaults to radio button&#39;s text. If no text content is available, a warning will be logged.</p></td></tr></tbody></table></div></div></div></div>");
$templateCache.put("app/main/components/material-docs/partials/api/material.components.radioButton/directive/mdRadioGroup.html","<div class=\"doc-content\"><header class=\"api-profile-header\"><h2 class=\"md-display-1\">{{currentDoc.name}} API Documentation</h2></header><div layout=\"row\" class=\"api-options-bar with-icon\"></div><div class=\"api-profile-description\"><p>The <code>&lt;md-radio-group&gt;</code> directive identifies a grouping container for the 1..n grouped radio buttons; specified using nested <code>&lt;md-radio-button&gt;</code> tags.</p><p>As per the <a href=\"http://www.google.com/design/spec/style/color.html#color-ui-color-application\">material design spec</a> the radio button is in the accent color by default. The primary color palette may be used with the <code>md-primary</code> class.</p><p>Note: <code>&lt;md-radio-group&gt;</code> and <code>&lt;md-radio-button&gt;</code> handle tabindex differently than the native <code>&lt;input type=&#39;radio&#39;&gt;</code> controls. Whereas the native controls force the user to tab through all the radio buttons, <code>&lt;md-radio-group&gt;</code> is focusable, and by default the <code>&lt;md-radio-button&gt;</code>s are not.</p></div><div><section class=\"api-section\"><h2 id=\"Usage\">Usage</h2><hljs lang=\"html\"><md-radio-group ng-model=\"selected\"><md-radio-button ng-repeat=\"d in colorOptions\" ng-value=\"d.value\" aria-label=\"{{ d.label }}\">{{ d.label }}</md-radio-button></md-radio-group></hljs></section><div class=\"api-param-section\"><h2>Attributes</h2><div class=\"api-param-table\"><table class=\"md-api-table\"><thead><tr><th>Parameter</th><th>Type</th><th>Description</th></tr></thead><tbody><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\"><b>* ng-model</b> <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-string\">string</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-string\">string</code></td><td class=\"description\"><p>Assignable angular expression to data-bind to.</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">md-no-ink <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-boolean\">boolean</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-boolean\">boolean</code></td><td class=\"description\"><p>Use of attribute indicates flag to disable ink ripple effects.</p></td></tr></tbody></table></div></div></div></div>");
$templateCache.put("app/main/components/material-docs/partials/api/material.components.select/directive/mdSelect.html","<div class=\"doc-content\"><header class=\"api-profile-header\"><h2 class=\"md-display-1\">{{currentDoc.name}} API Documentation</h2></header><div layout=\"row\" class=\"api-options-bar with-icon\"></div><div class=\"api-profile-description\"><p>Displays a select box, bound to an ng-model.</p></div><div><section class=\"api-section\"><h2 id=\"Usage\">Usage</h2><p>With a placeholder (label and aria-label are added dynamically)</p><hljs lang=\"html\"><md-input-container><md-select ng-model=\"someModel\" placeholder=\"Select a state\"><md-option ng-value=\"opt\" ng-repeat=\"opt in neighborhoods2\">{{ opt }}</md-option></md-select></md-input-container></hljs><p>With an explicit label</p><hljs lang=\"html\"><md-input-container><label>State</label><md-select ng-model=\"someModel\"><md-option ng-value=\"opt\" ng-repeat=\"opt in neighborhoods2\">{{ opt }}</md-option></md-select></md-input-container></hljs><h2 id=\"selects-and-object-equality\">Selects and object equality</h2><p>When using a <code>md-select</code> to pick from a list of objects, it is important to realize how javascript handles equality. Consider the following example:</p><hljs lang=\"js\">angular.controller(&#39;MyCtrl&#39;, function($scope) { $scope.users = [ { id: 1, name: &#39;Bob&#39; }, { id: 2, name: &#39;Alice&#39; }, { id: 3, name: &#39;Steve&#39; } ]; $scope.selectedUser = { id: 1, name: &#39;Bob&#39; }; });</hljs><hljs lang=\"html\"><div ng-controller=\"MyCtrl\"><md-select ng-model=\"selectedUser\"><md-option ng-value=\"user\" ng-repeat=\"user in users\">{{ user.name }}</md-option></md-select></div></hljs><p>At first one might expect that the select should be populated with &quot;Bob&quot; as the selected user. However, this is not true. To determine whether something is selected, <code>ngModelController</code> is looking at whether <code>$scope.selectedUser == (any user in $scope.users);</code>;</p><p>Javascript&#39;s <code>==</code> operator does not check for deep equality (ie. that all properties on the object are the same), but instead whether the objects are <em>the same object in memory</em>. In this case, we have two instances of identical objects, but they exist in memory as unique entities. Because of this, the select will have no value populated for a selected user.</p><p>To get around this, <code>ngModelController</code> provides a <code>track by</code> option that allows us to specify a different expression which will be used for the equality operator. As such, we can update our <code>html</code> to make use of this by specifying the <code>ng-model-options=&quot;{trackBy: &#39;$value.id&#39;}&quot;</code> on the <code>md-select</code> element. This converts our equality expression to be <code>$scope.selectedUser.id == (any id in $scope.users.map(function(u) { return u.id; }));</code> which results in Bob being selected as desired.</p><p>Working HTML:</p><hljs lang=\"html\"><div ng-controller=\"MyCtrl\"><md-select ng-model=\"selectedUser\" ng-model-options=\"{trackBy: \'$value.id\'}\"><md-option ng-value=\"user\" ng-repeat=\"user in users\">{{ user.name }}</md-option></md-select></div></hljs></section><div class=\"api-param-section\"><h2>Attributes</h2><div class=\"api-param-table\"><table class=\"md-api-table\"><thead><tr><th>Parameter</th><th>Type</th><th>Description</th></tr></thead><tbody><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\"><b>* ng-model</b> <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-expression\">expression</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-expression\">expression</code></td><td class=\"description\"><p>The model!</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">multiple <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-boolean\">boolean</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-boolean\">boolean</code></td><td class=\"description\"><p>Whether it&#39;s multiple.</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">md-on-close <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-expression\">expression</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-expression\">expression</code></td><td class=\"description\"><p>Expression to be evaluated when the select is closed.</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">placeholder <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-string\">string</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-string\">string</code></td><td class=\"description\"><p>Placeholder hint text.</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">aria-label <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-string\">string</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-string\">string</code></td><td class=\"description\"><p>Optional label for accessibility. Only necessary if no placeholder or explicit label is present.</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">md-container-class <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-string\">string</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-string\">string</code></td><td class=\"description\"><p>Class list to get applied to the <code>.md-select-menu-container</code> element (for custom styling).</p></td></tr></tbody></table></div></div></div></div>");
$templateCache.put("app/main/components/material-docs/partials/api/material.components.sidenav/directive/mdSidenav.html","<div class=\"doc-content\"><header class=\"api-profile-header\"><h2 class=\"md-display-1\">{{currentDoc.name}} API Documentation</h2></header><div layout=\"row\" class=\"api-options-bar with-icon\"></div><div class=\"api-profile-description\"><p>A Sidenav component that can be opened and closed programatically.</p><p>By default, upon opening it will slide out on top of the main content area.</p><p>For keyboard and screen reader accessibility, focus is sent to the sidenav wrapper by default. It can be overridden with the <code>md-autofocus</code> directive on the child element you want focused.</p></div><div><section class=\"api-section\"><h2 id=\"Usage\">Usage</h2><hljs lang=\"html\"><div layout=\"row\" ng-controller=\"MyController\"><md-sidenav md-component-id=\"left\" class=\"md-sidenav-left\">Left Nav!</md-sidenav><md-content>Center Content<md-button ng-click=\"openLeftMenu()\">Open Left Menu</md-button></md-content><md-sidenav md-component-id=\"right\" md-is-locked-open=\"$mdMedia(\'min-width: 333px\')\" class=\"md-sidenav-right\"><form><md-input-container><label for=\"testInput\">Test input</label> <input id=\"testInput\" type=\"text\" ng-model=\"data\" md-autofocus=\"\"></md-input-container></form></md-sidenav></div></hljs><hljs lang=\"js\">var app = angular.module(&#39;myApp&#39;, [&#39;ngMaterial&#39;]); app.controller(&#39;MyController&#39;, function($scope, $mdSidenav) { $scope.openLeftMenu = function() { $mdSidenav(&#39;left&#39;).toggle(); }; });</hljs></section><div class=\"api-param-section\"><h2>Attributes</h2><div class=\"api-param-table\"><table class=\"md-api-table\"><thead><tr><th>Parameter</th><th>Type</th><th>Description</th></tr></thead><tbody><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">md-is-open <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-expression\">expression</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-expression\">expression</code></td><td class=\"description\"><p>A model bound to whether the sidenav is opened.</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">md-component-id <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-string\">string</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-string\">string</code></td><td class=\"description\"><p>componentId to use with $mdSidenav service.</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">md-is-locked-open <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-expression\">expression</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-expression\">expression</code></td><td class=\"description\"><p>When this expression evalutes to true, the sidenav &#39;locks open&#39;: it falls into the content&#39;s flow instead of appearing over it. This overrides the <code>is-open</code> attribute.</p><p>The $mdMedia() service is exposed to the is-locked-open attribute, which can be given a media query or one of the <code>sm</code>, <code>gt-sm</code>, <code>md</code>, <code>gt-md</code>, <code>lg</code> or <code>gt-lg</code> presets. Examples:</p><ul><li><code>&lt;md-sidenav md-is-locked-open=&quot;shouldLockOpen&quot;&gt;&lt;/md-sidenav&gt;</code></li><li><code>&lt;md-sidenav md-is-locked-open=&quot;$mdMedia(&#39;min-width: 1000px&#39;)&quot;&gt;&lt;/md-sidenav&gt;</code></li><li><code>&lt;md-sidenav md-is-locked-open=&quot;$mdMedia(&#39;sm&#39;)&quot;&gt;&lt;/md-sidenav&gt;</code> (locks open on small screens)</li></ul></td></tr></tbody></table></div></div></div></div>");
$templateCache.put("app/main/components/material-docs/partials/api/material.components.sidenav/directive/mdSidenavFocus.html","<div class=\"doc-content\"><header class=\"api-profile-header\"><h2 class=\"md-display-1\">{{currentDoc.name}} API Documentation</h2></header><div layout=\"row\" class=\"api-options-bar with-icon\"></div><div class=\"api-profile-description\"><p><code>mdSidenavFocus</code> provides a way to specify the focused element when a sidenav opens. This is completely optional, as the sidenav itself is focused by default.</p></div><div><section class=\"api-section\"><h2 id=\"Usage\">Usage</h2><hljs lang=\"html\"><md-sidenav><form><md-input-container><label for=\"testInput\">Label</label> <input id=\"testInput\" type=\"text\" md-sidenav-focus=\"\"></md-input-container></form></md-sidenav></hljs></section></div></div>");
$templateCache.put("app/main/components/material-docs/partials/api/material.components.sidenav/service/$mdSidenav.html","<div class=\"doc-content\"><header class=\"api-profile-header\"><h2 class=\"md-display-1\">{{currentDoc.name}} API Documentation</h2></header><div layout=\"row\" class=\"api-options-bar with-icon\"></div><div class=\"api-profile-description\"><p><code>$mdSidenav</code> makes it easy to interact with multiple sidenavs in an app.</p></div><div><section class=\"api-section\"><h2 id=\"Usage\">Usage</h2><hljs lang=\"js\">// Async lookup for sidenav instance; will resolve when the instance is available $mdSidenav(componentId).then(function(instance) { $log.debug( componentId + &quot;is now ready&quot; ); }); // Async toggle the given sidenav; // when instance is known ready and lazy lookup is not needed. $mdSidenav(componentId) .toggle() .then(function(){ $log.debug(&#39;toggled&#39;); }); // Async open the given sidenav $mdSidenav(componentId) .open() .then(function(){ $log.debug(&#39;opened&#39;); }); // Async close the given sidenav $mdSidenav(componentId) .close() .then(function(){ $log.debug(&#39;closed&#39;); }); // Sync check to see if the specified sidenav is set to be open $mdSidenav(componentId).isOpen(); // Sync check to whether given sidenav is locked open // If this is true, the sidenav will be open regardless of close() $mdSidenav(componentId).isLockedOpen();</hljs></section></div></div>");
$templateCache.put("app/main/components/material-docs/partials/api/material.components.slider/directive/mdSlider.html","<div class=\"doc-content\"><header class=\"api-profile-header\"><h2 class=\"md-display-1\">{{currentDoc.name}} API Documentation</h2></header><div layout=\"row\" class=\"api-options-bar with-icon\"></div><div class=\"api-profile-description\"><p>The <code>&lt;md-slider&gt;</code> component allows the user to choose from a range of values.</p><p>As per the <a href=\"http://www.google.com/design/spec/style/color.html#color-ui-color-application\">material design spec</a> the slider is in the accent color by default. The primary color palette may be used with the <code>md-primary</code> class.</p><p>It has two modes: &#39;normal&#39; mode, where the user slides between a wide range of values, and &#39;discrete&#39; mode, where the user slides between only a few select values.</p><p>To enable discrete mode, add the <code>md-discrete</code> attribute to a slider, and use the <code>step</code> attribute to change the distance between values the user is allowed to pick.</p></div><div><section class=\"api-section\"><h2 id=\"Usage\">Usage</h2><p></p><h4>Normal Mode</h4><p></p><hljs lang=\"html\"><md-slider ng-model=\"myValue\" min=\"5\" max=\"500\"></md-slider></hljs><h4>Discrete Mode</h4><hljs lang=\"html\"><md-slider md-discrete=\"\" ng-model=\"myDiscreteValue\" step=\"10\" min=\"10\" max=\"130\"></md-slider></hljs></section><div class=\"api-param-section\"><h2>Attributes</h2><div class=\"api-param-table\"><table class=\"md-api-table\"><thead><tr><th>Parameter</th><th>Type</th><th>Description</th></tr></thead><tbody><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">md-discrete <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-boolean\">boolean</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-boolean\">boolean</code></td><td class=\"description\"><p>Whether to enable discrete mode.</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">step <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-number\">number</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-number\">number</code></td><td class=\"description\"><p>The distance between values the user is allowed to pick. Default 1.</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">min <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-number\">number</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-number\">number</code></td><td class=\"description\"><p>The minimum value the user is allowed to pick. Default 0.</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">max <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-number\">number</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-number\">number</code></td><td class=\"description\"><p>The maximum value the user is allowed to pick. Default 100.</p></td></tr></tbody></table></div></div></div></div>");
$templateCache.put("app/main/components/material-docs/partials/api/material.components.sticky/service/$mdSticky.html","<div class=\"doc-content\"><header class=\"api-profile-header\"><h2 class=\"md-display-1\">{{currentDoc.name}} API Documentation</h2></header><div layout=\"row\" class=\"api-options-bar with-icon\"></div><div class=\"api-profile-description\"><p>The <code>$mdSticky</code>service provides a mixin to make elements sticky.</p></div><div><section class=\"api-section\"><section class=\"api-section\"></section><div class=\"api-param-table\"><table class=\"md-api-table\"><thead><tr><th>Returns</th><th>Description</th></tr></thead><tbody><tr><td></td><td class=\"description\"><p>A <code>$mdSticky</code> function that takes three arguments:</p><ul><li><code>scope</code></li><li><code>element</code>: The element that will be &#39;sticky&#39;</li><li><code>elementClone</code>: A clone of the element, that will be shown when the user starts scrolling past the original element. If not provided, it will use the result of <code>element.clone()</code>.</li></ul></td></tr></tbody></table></div></section></div></div>");
$templateCache.put("app/main/components/material-docs/partials/api/material.components.subheader/directive/mdSubheader.html","<div class=\"doc-content\"><header class=\"api-profile-header\"><h2 class=\"md-display-1\">{{currentDoc.name}} API Documentation</h2></header><div layout=\"row\" class=\"api-options-bar with-icon\"></div><div class=\"api-profile-description\"><p>The <code>&lt;md-subheader&gt;</code> directive is a subheader for a section. By default it is sticky. You can make it not sticky by applying the <code>md-no-sticky</code> class to the subheader.</p></div><div><section class=\"api-section\"><h2 id=\"Usage\">Usage</h2><hljs lang=\"html\"><md-subheader>Online Friends</md-subheader></hljs></section></div></div>");
