$templateCache.put("app/main/components/material-docs/partials/api/material.core/service/$mdMedia.html","<div class=\"doc-content\"><header class=\"api-profile-header\"><h2 class=\"md-display-1\">{{currentDoc.name}} API Documentation</h2></header><div layout=\"row\" class=\"api-options-bar with-icon\"></div><div class=\"api-profile-description\"><p><code>$mdMedia</code> is used to evaluate whether a given media query is true or false given the current device&#39;s screen / window size. The media query will be re-evaluated on resize, allowing you to register a watch.</p><p><code>$mdMedia</code> also has pre-programmed support for media queries that match the layout breakpoints:</p><table class=\"md-api-table\"><thead><tr><th>Breakpoint</th><th>mediaQuery</th></tr></thead><tbody><tr><td>xs</td><td>(max-width: 599px)</td></tr><tr><td>gt-xs</td><td>(min-width: 600px)</td></tr><tr><td>sm</td><td>(min-width: 600px) and (max-width: 959px)</td></tr><tr><td>gt-sm</td><td>(min-width: 960px)</td></tr><tr><td>md</td><td>(min-width: 960px) and (max-width: 1279px)</td></tr><tr><td>gt-md</td><td>(min-width: 1280px)</td></tr><tr><td>lg</td><td>(min-width: 1280px) and (max-width: 1919px)</td></tr><tr><td>gt-lg</td><td>(min-width: 1920px)</td></tr><tr><td>xl</td><td>(min-width: 1920px)</td></tr></tbody></table><p>See Material Design&#39;s <a href=\"https://www.google.com/design/spec/layout/adaptive-ui.html\">Layout - Adaptive UI</a> for more details.</p><p><a href=\"https://www.google.com/design/spec/layout/adaptive-ui.html\"><img src=\"https://material-design.storage.googleapis.com/publish/material_v_4/material_ext_publish/0B8olV15J7abPSGFxemFiQVRtb1k/layout_adaptive_breakpoints_01.png\" width=\"100%\" height=\"100%\"></a></p></div><div><section class=\"api-section\"><h2 id=\"Usage\">Usage</h2><hljs lang=\"js\">app.controller(&#39;MyController&#39;, function($mdMedia, $scope) { $scope.$watch(function() { return $mdMedia(&#39;lg&#39;); }, function(big) { $scope.bigScreen = big; }); $scope.screenIsSmall = $mdMedia(&#39;sm&#39;); $scope.customQuery = $mdMedia(&#39;(min-width: 1234px)&#39;); $scope.anotherCustom = $mdMedia(&#39;max-width: 300px&#39;); });</hljs></section><section class=\"api-section\"><section class=\"api-section\"></section><div class=\"api-param-table\"><table class=\"md-api-table\"><thead><tr><th>Returns</th><th>Description</th></tr></thead><tbody><tr><td><code class=\"api-type label type-hint type-hint-boolean\">boolean</code></td><td class=\"description\"><p>a boolean representing whether or not the given media query is true or false.</p></td></tr></tbody></table></div></section></div></div>");
$templateCache.put("app/main/components/material-docs/partials/api/material.core/service/$mdTabInkRipple.html","<div class=\"doc-content\"><header class=\"api-profile-header\"><h2 class=\"md-display-1\">{{currentDoc.name}} API Documentation</h2></header><div layout=\"row\" class=\"api-options-bar with-icon\"></div><div class=\"api-profile-description\"><p>Provides ripple effects for md-tabs. See $mdInkRipple service for all possible configuration options.</p></div><div><section class=\"api-section\"><div class=\"api-param-section\"><h2>Arguments</h2><div class=\"api-param-table\"><table class=\"md-api-table\"><thead><tr><th>Parameter</th><th>Type</th><th>Description</th></tr></thead><tbody><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">scope <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-object\">object</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-object\">object</code></td><td class=\"description\"><p>Scope within the current context</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">element <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-object\">object</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-object\">object</code></td><td class=\"description\"><p>The element the ripple effect should be applied to</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">options <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-object\">object</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-object\">object</code></td><td class=\"description\"><p>(Optional) Configuration options to override the defaultripple configuration</p></td></tr></tbody></table></div></div><section class=\"api-section\"></section></section></div></div>");
$templateCache.put("app/main/components/material-docs/partials/api/material.core.gestures/service/$mdGestureProvider.html","<div class=\"doc-content\"><header class=\"api-profile-header\"><h2 class=\"md-display-1\">{{currentDoc.name}} API Documentation</h2></header><div layout=\"row\" class=\"api-options-bar with-icon\"></div><div class=\"api-profile-description\"><p>In some scenarios on Mobile devices (without jQuery), the click events should NOT be hijacked. <code>$mdGestureProvider</code> is used to configure the Gesture module to ignore or skip click hijacking on mobile devices.</p><hljs lang=\"js\">app.config(function($mdGestureProvider) { // For mobile devices without jQuery loaded, do not // intercept click events during the capture phase. $mdGestureProvider.skipClickHijack(); });</hljs></div><div></div></div>");
$templateCache.put("app/main/components/material-docs/partials/api/material.core.ripple/directive/mdInkRipple.html","<div class=\"doc-content\"><header class=\"api-profile-header\"><h2 class=\"md-display-1\">{{currentDoc.name}} API Documentation</h2></header><div layout=\"row\" class=\"api-options-bar with-icon\"></div><div class=\"api-profile-description\"><p>The <code>md-ink-ripple</code> directive allows you to specify the ripple color or id a ripple is allowed.</p></div><div><section class=\"api-section\"><h2 id=\"Usage\">Usage</h2><h3 id=\"string-values\">String values</h3><hljs lang=\"html\"><any md-ink-ripple=\"#FF0000\">Ripples in red</any><any md-ink-ripple=\"false\">Not rippling</any></hljs><h3 id=\"interpolated-values\">Interpolated values</h3><hljs lang=\"html\"><any md-ink-ripple=\"{{ randomColor() }}\">Ripples with the return value of &#39;randomColor&#39; function</any><any md-ink-ripple=\"{{ canRipple() }}\">Ripples if &#39;canRipple&#39; function return value is not &#39;false&#39; or &#39;0&#39;</any></hljs></section><div class=\"api-param-section\"><h2>Attributes</h2><div class=\"api-param-table\"><table class=\"md-api-table\"><thead><tr><th>Parameter</th><th>Type</th><th>Description</th></tr></thead><tbody><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\"><b>* md-ink-ripple</b> <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-string\">string</code><code class=\"api-type label type-hint type-hint-boolean\">boolean</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-string\">string</code><code class=\"api-type label type-hint type-hint-boolean\">boolean</code></td><td class=\"description\"><p>A color string <code>#FF0000</code> or boolean (<code>false</code> or <code>0</code>) for preventing ripple</p></td></tr></tbody></table></div></div></div></div>");
$templateCache.put("app/main/components/material-docs/partials/api/material.core.ripple/service/$mdInkRipple.html","<div class=\"doc-content\"><header class=\"api-profile-header\"><h2 class=\"md-display-1\">{{currentDoc.name}} API Documentation</h2></header><div layout=\"row\" class=\"api-options-bar with-icon\"></div><div class=\"api-profile-description\"><p><code>$mdInkRipple</code> is a service for adding ripples to any element</p></div><div><section class=\"api-section\"><h2 id=\"Usage\">Usage</h2><hljs lang=\"js\">app.factory(&#39;$myElementInkRipple&#39;, function($mdInkRipple) { return { attach: function (scope, element, options) { return $mdInkRipple.attach(scope, element, angular.extend({ center: false, dimBackground: true }, options)); } }; }); app.controller(&#39;myController&#39;, function ($scope, $element, $myElementInkRipple) { $scope.onClick = function (ev) { $myElementInkRipple.attach($scope, angular.element(ev.target), { center: true }); } });</hljs></section><section class=\"api-section\"><h2>Methods</h2><br><ul class=\"methods\"><li id=\"attach\"><h3 class=\"method-function-syntax\"><code class=\"method-function-syntax\">$mdInkRipple.attach([scope], [element], [options]);\r\n</code></h3><div class=\"service-desc\"><p>Attaching given scope, element and options to inkRipple controller</p></div><div class=\"method-param-table\"><table class=\"md-api-table\"><thead><tr><th>Parameter</th><th>Type</th><th>Description</th></tr></thead><tbody><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">scope <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-object\">object</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-object\">object</code></td><td class=\"description\"><p>Scope within the current context</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">element <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-object\">object</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-object\">object</code></td><td class=\"description\"><p>The element the ripple effect should be applied to</p></td></tr><tr class=\"api-params-item\"><td style=\"white-space: nowrap;\">options <span hide=\"\" show-sm=\"\"><code class=\"api-type label type-hint type-hint-object\">object</code></span></td><td style=\"white-space: nowrap;\"><code class=\"api-type label type-hint type-hint-object\">object</code></td><td class=\"description\"><p>(Optional) Configuration options to override the defaultRipple configuration</p><ul><li><code>center</code> - Whether the ripple should start from the center of the container element</li><li><code>dimBackground</code> - Whether the background should be dimmed with the ripple color</li><li><code>colorElement</code> - The element the ripple should take its color from, defined by css property <code>color</code></li><li><code>fitRipple</code> - Whether the ripple should fill the element</li></ul></td></tr></tbody></table></div></li></ul></section></div></div>");
